import { MPTab, MPTabProps } from '@mp-frontend/core-components';

import useDnd, { useDndProps } from 'hooks/useDnd';
import DragTypes from 'types/enums/DragTypes';

export interface DraggableTabProps extends MPTabProps {
  canDrag: boolean;
  dropCallback: useDndProps['onDrop'];
  hoverCallback: useDndProps['onHover'];
  value: string;
}

export default function DraggableTab({
  value,
  canDrag,
  hoverCallback,
  dropCallback,
  ...props
}: DraggableTabProps) {
  const [{ style: dragStyle }, dndRef] = useDnd(
    { id: value },
    canDrag,
    DragTypes.Tab,
    hoverCallback,
    dropCallback
  );

  return <MPTab ref={dndRef} style={dragStyle} {...props} />;
}
