import { useCallback, useEffect, useRef, useState } from 'react';
import { Formik } from 'formik';
import { debounce } from 'lodash';
import { useMutation } from 'react-relay';
import { SelectorStoreUpdater } from 'relay-runtime';
import * as Yup from 'yup';

import {
  MPActionButton,
  MPCheckbox,
  MPColorValue,
  MPDialog,
  MPFonts,
  MPStyledTextField,
  useIsMobile,
} from '@mp-frontend/core-components';
import { SearchIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import AddCategoryQL, {
  CategoryAddMutation,
} from 'graphql/__generated__/CategoryAddMutation.graphql';
import EditCategoryQL, {
  CategoryEditMutation,
  CategoryEditMutation$data,
} from 'graphql/__generated__/CategoryEditMutation.graphql';
import { CategoryType, MpErrors } from 'types/__generated__/graphql';

import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import { useGalleryCollectionPaginationFragmentQueryRef } from 'pages/profiles/useGalleryCollectionFragment';
import CSSGlobal from 'types/enums/css/Global';
import { Category } from 'types/graphql/Category';
import emptyFunc from 'utils/emptyFunc';

import { useGalleryPaginationFragmentQueryRef } from '../../useGalleryPaginationFragment';
import CategoryDialogMasonry from './masonry';

import * as styles from 'css/pages/profiles/dialog/EditCategory/index.module.css';

interface CategoryDialogProps {
  categoryQueryRef:
    | useGalleryCollectionPaginationFragmentQueryRef
    | useGalleryPaginationFragmentQueryRef;
  categoryType: CategoryType;
  defaultCategorySlug: string;
  defaultCollectionSlug: string;
  invalidateTabs: () => void;
  isOpen: boolean;
  mode: 'update' | 'create';
  setOpen: (open: boolean) => void;
  addCategoryCallback?: (category: Category) => void;
  category?: Category;
  editCategoryCallback?: SelectorStoreUpdater<CategoryEditMutation$data>;
  openDeleteCategoryDialog?: () => void;
}

export default function CategoryDialog({
  isOpen,
  setOpen,
  mode,
  categoryType,
  categoryQueryRef,
  defaultCategorySlug,
  defaultCollectionSlug,
  editCategoryCallback,
  addCategoryCallback,
  openDeleteCategoryDialog,
  invalidateTabs,
  category,
}: CategoryDialogProps) {
  const galleryRef = useRef(null);

  const [filterInputValue, setFilterInputValue] = useState('');
  const [filterValue, setFilterValue] = useState('');
  const debouncedSetFilterValue = debounce((val) => setFilterValue(val), 250);

  const [isFocused, setIsFocused] = useState(false);
  const [selected, setSelected] = useState<Set<string>>(new Set());
  const closeDialog = useCallback(() => {
    setSelected(new Set());
    setOpen(false);
    setFilterInputValue('');
    setFilterValue('');
  }, [setOpen]);

  const CategorySchema = Yup.object().shape({
    categoryName: Yup.string().required('Required'),
  });

  const formikPropsRef = useRef({
    handleSubmit: emptyFunc,
  });
  const [isDirty, setIsDirty] = useState(false);

  const actionButtons = [
    <MPActionButton
      size="large"
      key="save"
      fullWidth
      onClick={formikPropsRef.current.handleSubmit}
      disabled={
        category ? !isDirty && !selected.size : !isDirty || !selected.size
      }
    >
      {mode === 'update' ? 'Update' : 'Save'}
    </MPActionButton>,
  ];
  if (mode === 'update' && openDeleteCategoryDialog) {
    actionButtons.unshift(
      <MPActionButton
        size="large"
        key="delete"
        fullWidth
        variant="secondary"
        onClick={openDeleteCategoryDialog}
      >
        Delete Gallery
      </MPActionButton>
    );
  }

  const [addCategory] = useMutation<CategoryAddMutation>(AddCategoryQL);

  const [editCategory] = useMutation<CategoryEditMutation>(EditCategoryQL);

  const isMobile = useIsMobile();

  return (
    <MPDialog
      onClose={closeDialog}
      classes={{ root: styles.categoryDialog }}
      open={isOpen}
      actionButton={actionButtons}
      sx={{
        '& .MuiDialog-paper': {
          maxHeight: 'var(--maxDialogHeight)',
          width: isMobile ? '100%' : '600px',
        },
      }}
      title={mode === 'update' ? 'Edit Gallery' : 'Add New Gallery'}
    >
      <DefaultErrorBoundary>
        <Formik
          initialValues={{
            categoryName: category?.categoryName ?? '',
            description: category?.description ?? '',
            hidden: !!category?.hidden,
            isDefault:
              !!(
                categoryType === CategoryType.Store &&
                category &&
                defaultCategorySlug === category.categorySlug
              ) ||
              !!(
                categoryType === CategoryType.Collection &&
                category &&
                defaultCollectionSlug === category.categorySlug
              ),
          }}
          validationSchema={CategorySchema}
          enableReinitialize
          onSubmit={(values, { setSubmitting, setFieldError }) => {
            if (category) {
              editCategory({
                onCompleted: () => {
                  setSubmitting(false);
                  closeDialog();
                  invalidateTabs();
                },
                onError: (error) => {
                  if (
                    error.name === MpErrors.CategoryNameFormat ||
                    error.name === MpErrors.CategoryNameTaken ||
                    error.name === MpErrors.CategoryReservedName
                  )
                    setFieldError('categoryName', error.message);
                },
                updater: (store, resp) => {
                  if (editCategoryCallback) editCategoryCallback(store, resp);
                },
                variables: {
                  ...values,
                  pk: parseInt(category.pk, 10),
                  productIds: Array.from(selected).map((id) =>
                    parseInt(id, 10)
                  ),
                },
              });
            } else {
              addCategory({
                onCompleted: (_data) => {
                  setSubmitting(false);
                  closeDialog();
                  invalidateTabs();
                  if (addCategoryCallback)
                    addCategoryCallback(_data.createStoreCategory.category);
                },
                onError: (error) => {
                  if (
                    error.name === MpErrors.CategoryNameFormat ||
                    error.name === MpErrors.CategoryNameTaken ||
                    error.name === MpErrors.CategoryReservedName
                  )
                    setFieldError('categoryName', error.message);
                },
                variables: {
                  ...values,
                  categoryType,
                  productIds: Array.from(selected).map((id) =>
                    parseInt(id, 10)
                  ),
                },
              });
            }
            setSubmitting(true);
          }}
        >
          {(props) => {
            formikPropsRef.current = props;
            // This should be fine since the callback isn't conditionally called.
            /* eslint-disable-next-line react-hooks/rules-of-hooks */
            useEffect(() => {
              setIsDirty(props.dirty);
            }, [props.dirty]);

            return (
              <div
                className={joinClasses(
                  CSSGlobal.Flex.Col,
                  styles.nonHeaderContent
                )}
              >
                <MPStyledTextField
                  placeholder="New Gallery"
                  label="Gallery Name"
                  name="categoryName"
                  onChange={props.handleChange}
                  value={props.values.categoryName}
                />
                <MPStyledTextField
                  placeholder="Gallery Description"
                  rows={5}
                  name="description"
                  label="Gallery Description"
                  onChange={props.handleChange}
                  value={props.values.description}
                />
                <div
                  className={joinClasses(
                    CSSGlobal.Flex.Col,
                    styles.checkboxRow
                  )}
                >
                  <MPCheckbox
                    name="isDefault"
                    isChecked={props.values.isDefault}
                    onChange={props.handleChange}
                    label="Set as Default Gallery"
                  />
                  <MPCheckbox
                    name="hidden"
                    isChecked={props.values.hidden}
                    onChange={props.handleChange}
                    label="Hide Gallery"
                  />
                </div>
                <div>
                  <div
                    className={joinClasses(
                      CSSGlobal.Flex.Row,
                      styles.artworkInstructions,
                      MPFonts.inputLabel
                    )}
                  >
                    <span>Add Artwork</span>
                  </div>
                  <div className={CSSGlobal.Flex.Row}>
                    <div
                      className={joinClasses(
                        styles.searchWrapper,
                        isFocused ? 'isFocused' : undefined
                      )}
                    >
                      <span className={styles.searchIconWrapper}>
                        <SearchIcon
                          className={styles.searchIconSVG}
                          htmlColor={
                            isFocused
                              ? MPColorValue.PrimaryMain
                              : MPColorValue.Grey500
                          }
                          alt="Update Category"
                        />
                      </span>
                      <input
                        className={styles.filter}
                        type="text"
                        name="item-filter"
                        value={filterInputValue}
                        onChange={(event) => {
                          setFilterInputValue(event.target.value);
                          debouncedSetFilterValue(event.target.value);
                        }}
                        placeholder="Filter Your Artworks"
                        autoCapitalize="none"
                        autoComplete="off"
                        autoCorrect="off"
                        spellCheck="false"
                        onFocus={() => setIsFocused(true)}
                        onBlur={() => setIsFocused(false)}
                      />
                    </div>
                  </div>
                  <div className={styles.masonryWrapper} ref={galleryRef}>
                    <CategoryDialogMasonry
                      scrollRef={galleryRef}
                      filter={filterValue}
                      selected={selected}
                      categoryType={categoryType}
                      mode={mode}
                      paginationRef={categoryQueryRef}
                      category={category}
                      setSelected={setSelected}
                    />
                  </div>
                </div>
              </div>
            );
          }}
        </Formik>
      </DefaultErrorBoundary>
    </MPDialog>
  );
}
