import { Outlet, useOutletContext, useParams } from 'react-router-dom';

import GetCategories, {
  CategoryV3Query,
} from 'graphql/__generated__/CategoryV3Query.graphql';
import GetGallery, {
  GalleryQuery,
  GalleryQuery$variables,
} from 'graphql/__generated__/GalleryQuery.graphql';
import { CategoryType, ProfileAccountType } from 'types/__generated__/graphql';

import useLoadQuery from 'hooks/useLoadQuery';

import useProfileData, { useProfileDataQueryRef } from '../useProfileData';
import GalleryPageTabs from './GalleryPageTabs';

function Created() {
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);
  const { profileSlug } = useParams();
  const [categoryQueryRef] = useLoadQuery<CategoryV3Query>(GetCategories, {
    categoryType: CategoryType.Store,
    slug: profileSlug,
  });

  const queryParams: GalleryQuery$variables = {
    first: 1,
    hiddenOnCreate: false,
    slug: profileSlug,
  };
  const [allGalleryRef] = useLoadQuery<GalleryQuery>(GetGallery, queryParams);

  // TODO(brian): set the defaultCategory in defaultRedirectSlug once it's available
  return (
    <>
      {profileData.accountType === ProfileAccountType.Creator &&
        !!profileData.hasCollectedDigitalMedias && (
          <GalleryPageTabs currentTab="/:profileslug/gallery/created" />
        )}
      {!!categoryQueryRef && !!allGalleryRef && (
        <Outlet
          context={{
            allGalleryRef,
            categoryQueryRef,
            defaultRedirectSlug: '',
            profileQueryRef,
          }}
        />
      )}
    </>
  );
}

export default Created;
