import { MPDropdown, MPStyledTextField } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import useBodyRef from 'hooks/useBodyRef';
import useArtworkFilters from 'pages/explore/artwork/useArtworkFilters';
import useArtworkFilterState from 'pages/explore/artwork/UseArtworkFilterState';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';

function PriceFilter() {
  const bodyRef = useBodyRef();
  const { constrainMinPrice, constrainMaxPrice } = useArtworkFilters();
  const { price } = useArtworkFilterState();
  const [priceFilterState, setPriceFilterState] = price;
  const minPrice = priceFilterState.min ?? '';
  const maxPrice = priceFilterState.max ?? '';

  const updatePriceState = (key, value) => {
    setPriceFilterState((prevPrice) => ({ ...prevPrice, [key]: value }));
  };

  return (
    <MPDropdown title="Price Range" scrollableRef={bodyRef}>
      <div
        className={joinClasses(
          CSSGlobal.Flex.Row,
          CSSGlobal.Flex.CenteredRow,
          CSSGap[16]
        )}
      >
        <MPStyledTextField
          label="Minimum Price (USD)"
          placeholder="Min Price"
          value={minPrice}
          setValue={(val) => {
            updatePriceState('min', val);
          }}
          onBlur={constrainMinPrice}
          inputMode="numeric"
          startAdornment="$"
        />
        <MPStyledTextField
          label="Maximum Price (USD)"
          placeholder="Max Price"
          value={maxPrice}
          setValue={(val) => {
            updatePriceState('max', val);
          }}
          onBlur={constrainMaxPrice}
          inputMode="numeric"
          startAdornment="$"
        />
      </div>
    </MPDropdown>
  );
}

export default PriceFilter;
