import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';

const withShopOrActivityCheck = (Component) => {
  function Wrapper(props) {
    const [isShopOrActivityPage, setIsShopOrActivityPage] = useState(false);

    const location = useLocation();
    useEffect(() => {
      const pathNames = location.pathname?.split('/');
      const tabName = pathNames && pathNames.length > 2 && pathNames[2];
      if (['shop', 'activity'].includes(tabName)) {
        setIsShopOrActivityPage(true);
      } else {
        setIsShopOrActivityPage(false);
      }
    }, [location]);

    return <Component isShopOrActivityPage={isShopOrActivityPage} {...props} />;
  }
  Wrapper.displayName = `withShopOrActivityCheck(${
    Component.displayName ?? Component.name ?? 'AnonymousComponent'
  })`;

  return Wrapper;
};

export default withShopOrActivityCheck;
