import {
  PreloadedQuery,
  usePaginationFragment,
  usePreloadedQuery,
} from 'react-relay';

import GalleryProfileData, {
  GalleryProfile$key,
} from 'graphql/__generated__/GalleryProfile.graphql';
import GetGallery, {
  GalleryQuery,
} from 'graphql/__generated__/GalleryQuery.graphql';

export type useGalleryPaginationFragmentQueryRef = PreloadedQuery<GalleryQuery>;

// This hook as been split off to make it easier to reused for the category Dialog.
export default function useGalleryPaginationFragment(
  queryRef: useGalleryPaginationFragmentQueryRef
) {
  return usePaginationFragment<GalleryQuery, GalleryProfile$key>(
    GalleryProfileData,
    usePreloadedQuery<GalleryQuery>(GetGallery, queryRef)
  );
}
