import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import {
  ExternalCollectionType,
  ProductNodeType,
} from 'types/__generated__/graphql';

import ROUTES from 'constants/Routes';
import { Spread } from 'types/Spread';

import Creators from './Creators';
import HoverableProfileLink, {
  HoverableProfileLinkProps,
} from './HoverableProfileLink';

function mapCollectionToProfile(
  collection
): HoverableProfileLinkProps['profile'] {
  return {
    aboutBanner: '',
    floorPrice: collection.floorPrice,
    fullName: collection.collectionName,
    numOfOwners: collection.numOfOwners,
    profileImageUrl: collection.collectionLogoUrl,
    slug: collection.slug,
    totalItems: collection.totalItems,
    totalVolume: collection.totalVolume,
  };
}

export interface AttributionProps {
  item: {
    digitalMedia: {
      authorProfile: Spread<
        [
          HoverableProfileLinkProps['profile'],
          {
            fullName: string;
            slug: string;
          }
        ]
      >;
      collaboratorsProfile: ReadonlyArray<{
        slug: string;
        fullName?: string;
      }>;
      collection: {
        collectionName: string;
        collectionType: `${ExternalCollectionType}`;
        slug: string;
      };
    };
    liveProduct?: ProductNodeType;
  };
}

export default function Attribution({ item }: AttributionProps) {
  // TODO (brian): Under what conditions can we have a product with no store?
  const owner =
    !item.liveProduct?.store?.owner?.slug ||
    item.liveProduct.store.owner.slug === item.digitalMedia.authorProfile.slug
      ? undefined
      : item.liveProduct.store.owner;

  return (
    <div className={joinClasses('reset', MPFonts.attributeText)}>
      {(!item.digitalMedia.collection ||
        item.digitalMedia.collection.collectionType ===
          ExternalCollectionType.Internal) && (
        <Creators
          creator={{
            ...item.digitalMedia.authorProfile,
            href: ROUTES.PROFILE(item.digitalMedia.authorProfile.slug),
            name: item.digitalMedia.authorProfile.fullName,
          }}
          collaborators={item.digitalMedia.collaboratorsProfile.map(
            (collaborator) => ({
              ...item.digitalMedia.authorProfile,
              href: `/${collaborator.slug}`,
              name: collaborator.fullName,
            })
          )}
          owner={owner}
        />
      )}
      {item.digitalMedia.collection?.collectionType ===
        ExternalCollectionType.Standalone && (
        <HoverableProfileLink
          key={ROUTES.PROFILE(item.digitalMedia.collection.slug)}
          href={ROUTES.PROFILE(item.digitalMedia.collection.slug)}
          name={item.digitalMedia.collection.collectionName}
          profile={mapCollectionToProfile(item.digitalMedia.collection)}
          owner={owner}
        />
      )}
      {item.digitalMedia.collection?.collectionType ===
        ExternalCollectionType.Artist && (
        <div>
          <HoverableProfileLink
            key={ROUTES.PROFILE(item.digitalMedia.collection.slug)}
            href={ROUTES.PROFILE(item.digitalMedia.collection.slug)}
            name={item.digitalMedia.collection.collectionName}
            profile={mapCollectionToProfile(item.digitalMedia.collection)}
            owner={owner}
          />
          &nbsp;by&nbsp;
          <HoverableProfileLink
            key={ROUTES.PROFILE(item.digitalMedia.authorProfile.slug)}
            href={ROUTES.PROFILE(item.digitalMedia.authorProfile.slug)}
            name={item.digitalMedia.authorProfile.fullName}
            profile={item.digitalMedia.authorProfile}
            owner={owner}
          />
        </div>
      )}
    </div>
  );
}
