
.bannerContainer {
    composes: bannerRoot from 'css/pages/profiles/banner/Banner.module.css';
    width: 100%;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: calc(var(--bannerHeight));
}

.profilePhoto {
    composes: profilePhoto from 'css/pages/profiles/ProfileHeader.module.css';
    width: 36px;
    height: 36px;
    margin-left: 16px;
    border: 2px solid var(--color-commonWhite);
    z-index: 10000;
}

.profilePhotoWrapper {
    margin-top: -100%;
    position: relative;
    top: -5px;
}

.ownerProfilePhoto {
    composes: profilePhoto from 'css/pages/profiles/ProfileHeader.module.css';
    width: 24px;
    height: 24px;
    margin-left: 16px;
    border: 2px solid var(--color-commonWhite);
    z-index: 10000;
}

.creatorName {
    padding: 0 0 0 16px;
    color: var(--color-textPrimary);
}

.card {
    box-shadow: var(--shadow-hovered);
    text-align: left;
    border: 1px solid var(--color-grey600);
    border-radius: 0.25rem;
    background-color: var(--color-commonWhite);
    min-width: 320px;
    color: var(--color-textPrimary);
}

.ownerSection {
    align-items: center;
    border-top: 1px solid var(--color-primaryP20);
    gap: 4px;
}
