import { MPActionButton, MPChip } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import useArtworkFilterState from 'pages/explore/artwork/UseArtworkFilterState';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';
import CSSMargin from 'types/enums/css/Margin';
import CurrencyDisplayMode from 'types/enums/CurrencyDisplayMode';
import { NFTTraitsType } from 'types/graphql/NFT';
import generatePriceString from 'utils/currency/generatePricing';

interface ShopChipsProps {
  collectionTraits: Pick<NFTTraitsType, 'value' | 'valueSlug'>[];
}

function ShopChips({ collectionTraits }: ShopChipsProps) {
  const filterParams = useArtworkFilterState();
  const { traits, price, status } = filterParams;
  const [traitsValues, setTraitFilters] = traits;
  const [filterPrice, setFilterPrice] = price;
  const [, setStatus] = status;
  const minPriceFormatted = generatePriceString(
    parseFloat(filterPrice?.min || '0'),
    CurrencyDisplayMode.USD,
    { maximumFractionDigits: 0, minimumFractionDigits: 0 }
  );
  const maxPriceFormatted = generatePriceString(
    parseFloat(filterPrice?.max || '0'),
    CurrencyDisplayMode.USD,
    { maximumFractionDigits: 0, minimumFractionDigits: 0 }
  );

  const clearPrice = () => {
    setFilterPrice({ max: undefined, min: undefined });
  };

  const clearFilters = () => {
    setTraitFilters({});
    setStatus({});
    clearPrice();
  };

  const deleteTraitFilter = (key) => {
    setTraitFilters((prevTraits) => {
      const newTraits = { ...prevTraits };
      if (newTraits[key]) delete newTraits[key];
      return newTraits;
    });
  };

  const getLabel = (key) => {
    const trait = collectionTraits.find((t) => t.valueSlug === key);
    return trait?.value;
  };

  const [query, setQuery] = filterParams.query;
  const { max: maxPrice, min: minPrice } = filterParams.price[0];
  const traitsApplied = Object.entries(traitsValues).filter(
    ([, value]) => value !== undefined
  );
  const hasFiltersApplied = maxPrice || minPrice || traitsApplied.length > 0;

  return (
    <div
      className={joinClasses(
        CSSGlobal.Flex.InlineRow,
        CSSGap[12],
        hasFiltersApplied && CSSMargin.TOP[24],
        CSSGlobal.Width.maxContent
      )}
    >
      {!!query && (
        <MPChip
          label={`Results for: "${query}"`}
          onDelete={() => setQuery(undefined)}
        />
      )}
      {!!minPrice && !maxPrice && (
        <MPChip
          label={`Min Price: ${minPriceFormatted}`}
          onDelete={clearPrice}
        />
      )}

      {!!maxPrice && !minPrice && (
        <MPChip
          label={`Max Price: ${maxPriceFormatted}`}
          onDelete={clearPrice}
        />
      )}

      {!!minPrice && !!maxPrice && (
        <MPChip
          label={`Price: ${minPriceFormatted} - ${maxPriceFormatted}`}
          onDelete={clearPrice}
        />
      )}
      {traitsApplied.map(([key]) => (
        <MPChip
          key={key}
          label={getLabel(key)}
          onDelete={() => deleteTraitFilter(key)}
        />
      ))}
      {!!hasFiltersApplied && (
        <MPActionButton
          variant="secondary"
          size="small"
          onClick={clearFilters}
          className={CSSGlobal.Width.maxContent}
        >
          Clear All
        </MPActionButton>
      )}
    </div>
  );
}

export default ShopChips;
