import { useCallback, useRef, useState } from 'react';
import { noop } from 'lodash';
import { useMutation } from 'react-relay';
import { useOutletContext } from 'react-router-dom';
import { SelectorStoreUpdater } from 'relay-runtime';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';

import {
  MPActionButton,
  MPFonts,
  MPMasonry,
  MPMasonryProps,
  useIsMobile,
  useMPConfirmDialog,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import DeleteCategoryQL, {
  CategoryDeleteMutation,
} from 'graphql/__generated__/CategoryDeleteMutation.graphql';
import { CategoryEditMutation$data } from 'graphql/__generated__/CategoryEditMutation.graphql';
import { GalleryDigitalMediaReleaseFragment$data } from 'graphql/__generated__/GalleryDigitalMediaReleaseFragment.graphql';
import { CategoryType } from 'types/__generated__/graphql';

import { DefaultLoader } from 'components/DefaultSuspense';
import DigitalMediaItem from 'components/DigitalMediaItem/DigitalMediaItem';
import LEGACY_API from 'constants/LegacyAPI';
import useInfiniteQueryScroll from 'hooks/useInfiniteQueryScroll';
import CSSIn from 'types/enums/css';
import CSSGlobal from 'types/enums/css/Global';
import { Category } from 'types/graphql/Category';
import getToken from 'utils/getToken';

import CategoryDialog from '../dialog/EditCategory';
import useGalleryCollectionPaginationFragment, {
  useGalleryCollectionPaginationFragmentQueryRef,
} from '../useGalleryCollectionFragment';
import useGalleryPaginationFragment, {
  useGalleryPaginationFragmentQueryRef,
} from '../useGalleryPaginationFragment';
import useProfileData, { useProfileDataQueryRef } from '../useProfileData';
import useBackingDndData from './useBackingDndData';

import * as styles from 'css/pages/profiles/gallery/CategoryGallery.module.css';
import * as noContentStyles from 'css/pages/profiles/NoContent.module.css';

interface ICategoryMasonryProps extends Omit<MPMasonryProps, 'children'> {
  invalidateTabs: () => void;
  profileSlug: string;
  queryRef:
    | useGalleryCollectionPaginationFragmentQueryRef
    | useGalleryPaginationFragmentQueryRef;
  type: CategoryType;
  category?: Category;
  editCategoryCallback?: (category: Category) => void;
  isCurrentUser?: boolean;
  navigateToAll?: () => void;
}

export default function CategoryMasonry({
  type,
  queryRef,
  profileSlug,
  isCurrentUser,
  editCategoryCallback,
  category,
  invalidateTabs,
  navigateToAll,
  ...props
}: ICategoryMasonryProps) {
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);
  const visibilityRef = useRef(null);
  const scrollRef = useRef(null);
  // This is in theory safe, because in the component tree, each instance is gated by type such that we won't end up using a different one.
  // The API needs to change to a single instance to prevent this from blowing in our faces in the future.
  /* eslint-disable react-hooks/rules-of-hooks */
  const rawData =
    type === CategoryType.Collection
      ? useGalleryCollectionPaginationFragment(
          queryRef as useGalleryCollectionPaginationFragmentQueryRef
        )
      : useGalleryPaginationFragment(
          queryRef as useGalleryPaginationFragmentQueryRef
        );
  const {
    data: oData,
    hasMore,
    loading,
  } = useInfiniteQueryScroll({
    pageSize: 10,
    paginatedQueryResults: rawData,
    ref: visibilityRef,
    scrollRef,
  });
  /* eslint-enable react-hooks/rules-of-hooks */

  const data: Array<GalleryDigitalMediaReleaseFragment$data> = oData
    .filter((media) => media?.latestRelease?.liveProduct)
    .map((dm) => dm.latestRelease);

  // We roll our own since useMemo is not a semantic guarantee
  const [derivedData, hoverCallback, invalidateBackingData] = useBackingDndData(
    data,
    [category?.categorySlug]
  );

  const dropCallback = useCallback(() => {
    // TODO (brian): Once we have the back enums, or maybe better APIs!, use them
    const feedType =
      type === CategoryType.Store
        ? !category?.categorySlug
          ? 1
          : 3
        : !category?.categorySlug
        ? 2
        : 5;
    const headers = new Headers();
    headers.append('X-CSRFToken', getToken());
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const body = new URLSearchParams();
    body.append('feed_type', feedType.toString());
    derivedData.forEach((release) =>
      body.append('product_ids[]', release.liveProduct.pk.toString())
    );
    if (category?.pk) {
      body.append('category_id', category.pk);
    }

    fetch(LEGACY_API.MUTATE_DIGITAL_MEDIA_ORDERING(profileSlug), {
      body,
      headers,
      method: 'POST',
    });
  }, [derivedData, type, category?.categorySlug, profileSlug, category?.pk]);

  const [isCategoryDialogOpen, setCategoryDialogOpen] = useState(false);
  const openCategoryDialog = useCallback(
    () => setCategoryDialogOpen(true),
    [setCategoryDialogOpen]
  );

  const invalidateCategory: SelectorStoreUpdater<CategoryEditMutation$data> = (
    store,
    resp
  ) => {
    invalidateBackingData();
    store.get(profileData.id).invalidateRecord();
    store.get(rawData.data.results.__id).invalidateRecord();
    store.get(resp.editStoreCategory.categories.id).invalidateRecord();
    if (editCategoryCallback)
      editCategoryCallback(resp.editStoreCategory.category);
  };

  let setDeleteCategoryOpen = noop;
  const [deleteCategoryCall] =
    useMutation<CategoryDeleteMutation>(DeleteCategoryQL);
  const deleteCategory = useCallback(() => {
    deleteCategoryCall({
      onCompleted: () => {
        setDeleteCategoryOpen(false);
        if (navigateToAll) navigateToAll();
      },
      updater: (store) => {
        store.get(profileData.id).invalidateRecord();
      },
      variables: { id: category?.id },
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deleteCategoryCall, navigateToAll, category?.id, profileData.id]);

  const [, _setDeleteCategoryOpen, ConfirmDialog] = useMPConfirmDialog({
    content: (
      <div className={styles.deleteConfirmContent}>
        <p className="reset">Are you sure you want to delete this gallery?</p>
        <p className="reset">
          Deleting this gallery will not remove any of the artwork contained
          within
        </p>
      </div>
    ),
    onCancel: noop,
    onConfirm: deleteCategory,
    title: 'Delete Gallery',
  });
  setDeleteCategoryOpen = _setDeleteCategoryOpen;
  const openDeleteCategoryDialog = useCallback(
    () => setDeleteCategoryOpen(true),
    [setDeleteCategoryOpen]
  );

  const isExtraWide = useMediaQuery(useTheme().breakpoints.up('wide'));
  const isMobile = useIsMobile();
  const GalleryColumnNum = isExtraWide ? 4 : !isMobile ? 3 : 1;

  return (
    <div data-category={category?.categorySlug} ref={scrollRef}>
      {data.length === 0 && !hasMore && !category && (
        <>
          {type === CategoryType.Store && (
            <div
              className={joinClasses(
                CSSGlobal.Flex.CenteredCol,
                noContentStyles.noContent
              )}
            >
              <img width="128" src="/static/img/icons/empty_store.png" />
              <p
                className={joinClasses(
                  MPFonts.bodyMedium,
                  noContentStyles.noContentDisplayName
                )}
              >
                {profileData.displayName}
              </p>
              <p>hasn&#39;t created any Artworks.</p>
            </div>
          )}
          {type === CategoryType.Collection && (
            <div
              className={joinClasses(
                CSSGlobal.Flex.CenteredCol,
                noContentStyles.noContent
              )}
            >
              <img width="128" src="/static/img/icons/empty_gallery.png" />
              <p
                className={joinClasses(
                  MPFonts.bodyMedium,
                  noContentStyles.noContentDisplayName
                )}
              >
                {profileData.displayName}
              </p>
              <p>hasn&#39;t collected any Artworks.</p>
            </div>
          )}
        </>
      )}
      <p className={joinClasses(MPFonts.titleSmall, styles.categoryTitle)}>
        {category?.categoryName}
        {!!category?.categoryName && <>&nbsp;</>}
        {!!isCurrentUser && !!category && (
          <>
            <MPActionButton
              className={CSSIn.MARGIN.LEFT[16]}
              variant="tertiary"
              onClick={openCategoryDialog}
            >
              Edit&nbsp;Gallery
            </MPActionButton>
            <CategoryDialog
              invalidateTabs={invalidateTabs}
              categoryQueryRef={queryRef}
              isOpen={isCategoryDialogOpen}
              setOpen={setCategoryDialogOpen}
              mode="update"
              defaultCategorySlug={
                profileData.defaultCategory?.categorySlug ?? ''
              }
              defaultCollectionSlug={
                profileData.defaultCollection?.categorySlug ?? ''
              }
              editCategoryCallback={invalidateCategory}
              openDeleteCategoryDialog={openDeleteCategoryDialog}
              category={category}
              categoryType={type}
            />
            {ConfirmDialog}
          </>
        )}
      </p>
      <p className={MPFonts.bodyMedium}>{category?.description}</p>
      <MPMasonry columns={GalleryColumnNum} spacing={2} {...props}>
        {derivedData.map((release) => (
          <DigitalMediaItem
            key={release.id}
            dm={release.digitalMedia}
            release={release}
            link={release.liveProduct.productUrl}
            product={release.liveProduct}
            canDrag={!!profileData.isCurrentUser && !isMobile}
            hoverCallback={hoverCallback}
            dropCallback={dropCallback}
          />
        ))}
        <div ref={visibilityRef}>&nbsp;</div>
      </MPMasonry>
      {!!loading && <DefaultLoader />}
    </div>
  );
}
