import { useCallback } from 'react';

import { MPDropdownSelect } from '@mp-frontend/core-components';

import {
  ActivitySortTypeEnum,
  SortOrderEnum,
} from 'types/__generated__/graphql';

import GTM from 'GTM';
import useBodyRef from 'hooks/useBodyRef';

import UseActivityFilterState from './UseActivityFilterState';

import * as styles from 'css/pages/profiles/activity/activity.module.css';

const getSortLabel = (
  sortType: ActivitySortTypeEnum,
  sortOrder: SortOrderEnum
) => {
  if (sortType === ActivitySortTypeEnum.CreatedAt) {
    if (sortOrder === SortOrderEnum.Asc) return 'Oldest';

    if (sortOrder === SortOrderEnum.Desc) return 'Newest';
  } else if (sortType === ActivitySortTypeEnum.Price) {
    if (sortOrder === SortOrderEnum.Asc) return 'Lowest Price';

    if (sortOrder === SortOrderEnum.Desc) return 'Highest Price';
  }

  return '';
};

const FILTERS: (ActivitySortTypeEnum | SortOrderEnum)[][] = [
  [ActivitySortTypeEnum.CreatedAt, SortOrderEnum.Desc],
  [ActivitySortTypeEnum.CreatedAt, SortOrderEnum.Asc],
  [ActivitySortTypeEnum.Price, SortOrderEnum.Asc],
  [ActivitySortTypeEnum.Price, SortOrderEnum.Desc],
];

const FILTER_CONFIG = Object.fromEntries(
  FILTERS.map(([sortType, sortOrder]) => [
    `${sortType}-${sortOrder}`,
    getSortLabel(sortType as ActivitySortTypeEnum, sortOrder as SortOrderEnum),
  ])
);

export default function FilterSortButton() {
  const bodyRef = useBodyRef();
  const filterParams = UseActivityFilterState();

  const [sortType, setSortType] = filterParams.sortType;
  const [sortOrder, setSortOrder] = filterParams.sortOrder;

  const handleChange = useCallback(
    (values: string[]) => {
      const [newSortType, newSortOrder] = values[0].split('-') as [
        ActivitySortTypeEnum,
        SortOrderEnum
      ];

      setSortType(newSortType);
      setSortOrder(newSortOrder);
      GTM.profile.trackActivitySortByChange(
        getSortLabel(newSortType, newSortOrder)
      );
    },
    [setSortType, setSortOrder]
  );

  return (
    <div className={styles.filterRow}>
      <MPDropdownSelect
        align="right"
        config={FILTER_CONFIG}
        scrollableRef={bodyRef}
        selectionType="single"
        title="Sorting"
        values={[`${sortType}-${sortOrder}`] as string[]}
        onChange={handleChange}
      />
    </div>
  );
}
