import { useEffect, useRef, useState } from 'react';
import {
  PreloadedQuery,
  usePaginationFragment,
  usePreloadedQuery,
} from 'react-relay';

import { MPFonts, useIsMobile } from '@mp-frontend/core-components';

import ActivityPaginatedFragmentNode, {
  ActivityPaginatedFragment$key,
} from 'graphql/__generated__/ActivityPaginatedFragment.graphql';
import ActivityPaginatedQueryNode, {
  ActivityPaginatedQuery,
} from 'graphql/__generated__/ActivityPaginatedQuery.graphql';

import { DefaultLoader } from 'components/DefaultSuspense';
import useInfiniteQueryScroll from 'hooks/useInfiniteQueryScroll';

import ActivityMobileTable from './ActivityMobileTable';
import ActivityTable from './ActivityTable';

export const EVENT_ACTIVITY_GRID_READY = 'ACTIVITY_GRID_READY';

export interface ActivityGridInnerProps {
  queryRef: PreloadedQuery<ActivityPaginatedQuery>;
  noResults?: () => JSX.Element;
}

function NoResults() {
  return (
    <div className="centerText">
      <h2 className={MPFonts.headline4}>
        There are no activities here at this time. Please check back later!
      </h2>
    </div>
  );
}

export default function ActivityGridInner({
  queryRef,
  noResults,
}: ActivityGridInnerProps) {
  const visibilityRef = useRef(null);
  const scrollRef = useRef(null);

  const { data: activities, loading } = useInfiniteQueryScroll({
    getConnectionField: (data) => data.results,
    pageSize: 10,
    paginatedQueryResults: usePaginationFragment<
      ActivityPaginatedQuery,
      ActivityPaginatedFragment$key
    >(
      ActivityPaginatedFragmentNode,
      usePreloadedQuery<ActivityPaginatedQuery>(
        ActivityPaginatedQueryNode,
        queryRef
      )
    ),
    ref: visibilityRef,
    scrollRef,
  });

  const domSelf = useRef(null);

  const [isInitialized, setIsInitialized] = useState(false);
  useEffect(() => {
    if (isInitialized || loading) return;
    setIsInitialized(true);
    domSelf?.current.dispatchEvent(
      new Event(EVENT_ACTIVITY_GRID_READY, { bubbles: true })
    );
  }, [isInitialized, loading]);

  const NoResultsComp = noResults ?? NoResults;
  const isMobile = useIsMobile();

  return (
    <div ref={domSelf}>
      {!loading && activities.length === 0 ? (
        <NoResultsComp />
      ) : (
        <div ref={scrollRef}>
          {isMobile ? (
            <ActivityMobileTable rowData={activities} />
          ) : (
            <ActivityTable rowData={activities} />
          )}
          <div ref={visibilityRef} className="gridSpanRow">
            &nbsp;
          </div>
        </div>
      )}
      {!!loading && <DefaultLoader />}
    </div>
  );
}
