import { ReactNode } from 'react';

import { MPColorClass, MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';

import * as styles from 'css/pages/profiles/filters/FilterGroup.module.css';

interface FiltersGroupProps {
  children: ReactNode;
  title: string;
}

export function FiltersGroup({ title, children }: FiltersGroupProps) {
  return (
    <div className={joinClasses(CSSGlobal.Flex.InlineRow, CSSGap[12])}>
      <div
        className={joinClasses(
          MPFonts.textSmallSemiBold,
          MPColorClass.SolidNeutralGray5,
          CSSGlobal.Flex.CenteredCol
        )}
      >
        {title}
      </div>
      <div className={joinClasses(CSSGlobal.Flex.InlineRow, CSSGap[12])}>
        {children}
      </div>
    </div>
  );
}

interface FiltersSectionProps {
  children: ReactNode;
  inline?: boolean;
  scrollable?: boolean;
}

export function FiltersSection({
  children,
  inline = false,
  scrollable = false,
}: FiltersSectionProps) {
  return (
    <div
      className={joinClasses(
        {
          [CSSGlobal.Flex.InlineRow]: inline,
          [CSSGlobal.Flex.Row]: !inline,
          [styles.slide]: scrollable,
        },
        CSSGap[24]
      )}
    >
      {children}
    </div>
  );
}
