import { Spread } from 'types/Spread';

import Attribution, { AttributionProps } from './Attribution';
import Header from './Header';
import Pricing from './Pricing';

import * as styles from 'css/components/bar/DigitalMediaItemBar.module.css';

export interface DigitalMediaItemBarProps {
  dm: Spread<
    [
      AttributionProps['item']['digitalMedia'],
      {
        title: string;
        totalSupply: number;
      }
    ]
  >;
  href: string;
  printEdition: number;
  product: AttributionProps['item']['liveProduct'];
  className?: string;
}

function DigitalMediaItemBar({
  dm,
  product,
  printEdition,
  href,
  className = '',
}: DigitalMediaItemBarProps) {
  return (
    <div className={`${className} ${styles.bar}`}>
      <div className={styles.barContent}>
        <Header
          printEdition={printEdition}
          totalSupply={dm.totalSupply}
          href={href}
          title={dm.title}
        />
        <Attribution item={{ digitalMedia: dm, liveProduct: product }} />
        <Pricing product={product} />
      </div>
    </div>
  );
}

export default DigitalMediaItemBar;
