import { useRecoilValue } from 'recoil';

import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import SelectedCurrencyDisplayMode, {
  SelectedCurrencyDisplaySymbol,
} from 'state/SelectedCurrencyDisplayMode';
import CSSGlobal from 'types/enums/css/Global';
import CurrencyDisplayMode from 'types/enums/CurrencyDisplayMode';
import toNumericShorthand from 'utils/numericShorthand';

import * as styles from 'css/components/MarketInsights.module.css';

interface MarketInsightsProps {
  floorPrice: { floorPriceInEth: number; floorPriceInUsd: number };
  numOfOwners: number;
  totalItems: number;
  totalVolume: { totalVolumeInEth: number; totalVolumeInUsd: number };
  numOfItemsListed?: number;
  replacementClassName?: string;
  replacementEntryClassName?: string;
  replacementLabelClassName?: string;
  replacementValueClassName?: string;
}

export default function MarketInsights({
  totalVolume,
  floorPrice,
  numOfOwners,
  totalItems,
  numOfItemsListed,
  replacementClassName,
  replacementEntryClassName,
  replacementLabelClassName,
  replacementValueClassName,
}: MarketInsightsProps) {
  const currencyDisplayMode = useRecoilValue(SelectedCurrencyDisplayMode);
  const currencyDisplaySymbol = useRecoilValue(SelectedCurrencyDisplaySymbol);

  let floor = 'N/A';
  if (floorPrice.floorPriceInEth !== 0 || floorPrice.floorPriceInUsd !== 0) {
    floor = `${currencyDisplaySymbol}${toNumericShorthand(
      currencyDisplayMode === CurrencyDisplayMode.ETH
        ? floorPrice.floorPriceInEth
        : floorPrice.floorPriceInUsd
    )}`;
  }

  const volume = toNumericShorthand(
    currencyDisplayMode === CurrencyDisplayMode.ETH
      ? totalVolume.totalVolumeInEth
      : totalVolume.totalVolumeInUsd
  );
  const items = toNumericShorthand(totalItems);
  const owners = toNumericShorthand(numOfOwners);

  const listedItems =
    !!totalItems && numOfItemsListed != null
      ? Math.trunc((numOfItemsListed * 100) / totalItems).toString()
      : 'N/A';

  const rowClassName = replacementClassName || styles.insights;
  const entryClassName = joinClasses(
    CSSGlobal.Flex.Col,
    replacementEntryClassName || styles.insight
  );
  const labelClassName =
    replacementLabelClassName ||
    joinClasses(
      MPFonts.marketCardInsightsLabel,
      styles.marketCardInsightsLabel
    );

  const valueClassName =
    replacementValueClassName || MPFonts.marketCardInsightsText;

  return (
    <div className={joinClasses(CSSGlobal.Flex.Row, rowClassName)}>
      <div className={entryClassName}>
        <span className={labelClassName}>Artworks</span>
        <span className={valueClassName}>{items}</span>
      </div>
      <div className={entryClassName}>
        <span className={labelClassName}>Floor</span>
        <span className={valueClassName}>{floor}</span>
      </div>
      <div className={entryClassName}>
        <span className={labelClassName}>Sales</span>
        <span
          className={valueClassName}
        >{`${currencyDisplaySymbol}${volume}`}</span>
      </div>
      {parseInt(owners, 10) > 0 && (
        <div className={entryClassName}>
          <span className={labelClassName}>Collectors</span>
          <span className={valueClassName}>{owners}</span>
        </div>
      )}
      {numOfItemsListed != null && (
        <div className={entryClassName}>
          <span className={labelClassName}>Listed</span>
          <span className={valueClassName}>{listedItems}%</span>
        </div>
      )}
    </div>
  );
}
