import { useEffect, useState } from 'react';
import { useTheme } from '@mui/material/styles';

import { useRefState } from '@mp-frontend/core-utils';

import * as styles from 'css/components/DigitalMediaImage.module.css';

function DigitalMediaItem({
  metadata,
  media500xPreviewContent,
  media1000xPreviewContent,
}) {
  const [, forceRender] = useState(0);
  const [imageNode, imageNodeRef] = useRefState<HTMLImageElement>(null);

  // Force a render if image is already loaded.
  useEffect(() => {
    forceRender((r) => r + 1);
  }, [media500xPreviewContent, imageNode]);

  const totalPadding = (metadata.height / metadata.width) * 100;
  return (
    <>
      <img
        ref={imageNodeRef}
        style={imageNode?.complete ? {} : { visibility: 'hidden' }}
        className={styles.idDigitalMediaImage}
        sizes={`(max-width: ${
          useTheme().breakpoints.values.wide
        }px) 500px, 1000px`}
        alt="media_content"
        onLoad={() => {
          forceRender((r) => r + 1);
        }}
        onError={() => {
          forceRender((r) => r + 1);
        }}
        srcSet={`${media500xPreviewContent} 500w, ${media1000xPreviewContent} 1000w`}
      />
      {!imageNode?.complete && (
        <div
          className={styles.digitalMediaEffect}
          style={{ paddingTop: `${totalPadding}%` }}
        />
      )}
    </>
  );
}

export default DigitalMediaItem;
