import ActivityPaginatedQueryNode, {
  ActivityPaginatedQuery,
} from 'graphql/__generated__/ActivityPaginatedQuery.graphql';

import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import useLoadQuery from 'hooks/useLoadQuery';
import useActivityFilterState from 'pages/profiles/activity/UseActivityFilterState';

import ActivityGridInner, { ActivityGridInnerProps } from './ActivityGridInner';

interface ActivityGridProps {
  artist?: string;
  noResults?: ActivityGridInnerProps['noResults'];
}

export default function ActivityGrid({ artist, noResults }: ActivityGridProps) {
  const filterParams = useActivityFilterState();
  const queryParams: ActivityPaginatedQuery['variables'] = {
    currencyMode: filterParams.currencyMode[0],
    first: 10,
    sortOrder: filterParams.sortOrder[0],
    sortType: filterParams.sortType[0],
    storeCollectionSlug: artist,
  };
  const [queryRef] = useLoadQuery<ActivityPaginatedQuery>(
    ActivityPaginatedQueryNode,
    queryParams,
    'store-and-network'
  );

  return (
    <DefaultErrorBoundary>
      {!!queryRef && (
        <ActivityGridInner queryRef={queryRef} noResults={noResults} />
      )}
    </DefaultErrorBoundary>
  );
}
