import {
  MPColorClass,
  MPFonts,
  MPTooltip,
  MPTooltipProps,
  TooltipVariants,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileImg, ProfileMask } from 'pages/profiles/banner';
import CSSGlobal from 'types/enums/css/Global';
import RestProps from 'types/RestProps';
import getProfileImage from 'utils/getProfileImage';

import MarketInsights from './MarketInsights';

import * as styles from 'css/components/CreatorTooltip.module.css';

interface CardContentProps {
  attributorAboutBanner: string;
  attributorFullName: string;
  attributorLink: string;
  attributorProfileImageUrl: string;
  floorPrice: { floorPriceInEth: number; floorPriceInUsd: number };
  numOfOwners: number;
  totalItems: number;
  totalVolume: { totalVolumeInEth: number; totalVolumeInUsd: number };
  ownerFullName?: string;
  ownerLink?: string;
  ownerProfileImageUrl?: string;
}

function CardContent({
  attributorAboutBanner,
  attributorFullName,
  attributorProfileImageUrl,
  attributorLink,
  ownerFullName,
  ownerProfileImageUrl,
  ownerLink,
  totalVolume,
  floorPrice,
  numOfOwners,
  totalItems,
}: CardContentProps) {
  return (
    <div className={styles.card}>
      <div className={styles.bannerContainer}>
        <ProfileMask />
        <ProfileImg backgroundImage={attributorAboutBanner} />
        <div
          className={joinClasses(
            CSSGlobal.Flex.Row,
            styles.profilePhotoWrapper
          )}
        >
          <img
            className={styles.profilePhoto}
            src={getProfileImage(attributorProfileImageUrl)}
          />
        </div>
      </div>
      <div className={CSSGlobal.Flex.Row}>
        <div className={joinClasses(styles.creatorName, MPFonts.titleXS)}>
          <a
            href={attributorLink}
            target="_blank"
            rel="noreferrer"
            className="invisibleAnchor"
          >
            {attributorFullName}
          </a>
        </div>
      </div>
      <MarketInsights
        totalVolume={totalVolume}
        floorPrice={floorPrice}
        numOfOwners={numOfOwners}
        totalItems={totalItems}
      />
      {!!ownerFullName && (
        <div className={joinClasses(CSSGlobal.Flex.Row, styles.ownerSection)}>
          <img
            className={styles.ownerProfilePhoto}
            src={ownerProfileImageUrl}
          />
          <div>
            <p>
              <span
                className={joinClasses(
                  MPColorClass.PrimaryP45,
                  MPFonts.marketCardOwned
                )}
              >
                Owned By:
              </span>
              <br />
              <span className={MPFonts.marketCardArtist}>
                <a
                  href={ownerLink}
                  target="_blank"
                  rel="noreferrer"
                  className="invisibleAnchor"
                >
                  {ownerFullName}
                </a>
              </span>
            </p>
          </div>
        </div>
      )}
    </div>
  );
}

export interface ICreatorTooltipProps extends RestProps {
  children: MPTooltipProps['children'];
  user: {
    aboutBanner: string;
    floorPrice: { floorPriceInEth: number; floorPriceInUsd: number };
    fullName: string;
    numOfOwners: number;
    profileImageUrl: string;
    slug: string;
    totalItems: number;
    totalVolume: { totalVolumeInEth: number; totalVolumeInUsd: number };
  };
  enterDelay?: number;
  leaveDelay?: number;
  owner?: { profileImageUrl: string; slug: string; fullName?: string };
  placement?: MPTooltipProps['placement'];
  variant?: TooltipVariants;
}

export default function CreatorTooltip({
  children,
  user,
  owner,
  placement = 'top-start',
  variant = TooltipVariants.BLANK,
  enterDelay = 500,
  leaveDelay = 500,
  ...props
}: ICreatorTooltipProps) {
  return (
    <MPTooltip
      title={
        <CardContent
          attributorAboutBanner={user.aboutBanner}
          attributorFullName={user.fullName}
          attributorProfileImageUrl={user.profileImageUrl}
          attributorLink={`/${user.slug}`}
          ownerFullName={owner?.fullName}
          totalVolume={user.totalVolume}
          floorPrice={user.floorPrice}
          numOfOwners={user.numOfOwners}
          totalItems={user.totalItems}
          ownerProfileImageUrl={getProfileImage(owner?.profileImageUrl)}
          ownerLink={owner ? `/${owner.slug}` : undefined}
        />
      }
      placement={placement}
      enterDelay={enterDelay}
      leaveDelay={leaveDelay}
      variant={variant}
      {...props}
    >
      {children}
    </MPTooltip>
  );
}
