import { useParams } from 'react-router-dom';

import { MPTabsVariant } from '@mp-frontend/core-components';

import Tabs from 'components/Tabs';

const TabSx = { minHeight: '36px', minWidth: '32px', padding: 0 };

function GalleryPageTabs({ currentTab }: { currentTab: string }) {
  const { profileSlug } = useParams();

  const tabs = [
    {
      label: 'Creations',
      sx: TabSx,
      to: `/${profileSlug}/gallery/created`,
      value: '/:profileslug/gallery/created',
    },
    {
      label: 'Collected Artworks',
      sx: TabSx,
      to: `/${profileSlug}/gallery/collected`,
      value: '/:profileslug/gallery/collected',
    },
  ];

  return (
    <Tabs
      tabsSx={{ marginTop: '4px', minHeight: '36px' }}
      currentTab={currentTab}
      design={MPTabsVariant.Text}
      tabs={tabs}
    />
  );
}

export default GalleryPageTabs;
