import {
  MPCheckbox,
  MPColorClass,
  MPDropdown,
  MPFonts,
} from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import useBodyRef from 'hooks/useBodyRef';
import useArtworkFilterState from 'pages/explore/artwork/UseArtworkFilterState';
import CSSGap from 'types/enums/css/Gap';
import CSSGlobal from 'types/enums/css/Global';
import { NFTTraitsType } from 'types/graphql/NFT';

import * as styles from 'css/pages/profiles/filters/TraitsFilters.module.css';

interface TraitFilterProps {
  groupedTraits: Record<string, { items: NFTTraitsType[]; name: string }>;
}

function TraitFilter({ groupedTraits }: TraitFilterProps) {
  const filterParams = useArtworkFilterState();
  const { traits: traitFilters } = filterParams;
  const [traitsApplied, setTraitFilters] = traitFilters;
  const bodyRef = useBodyRef();

  const updateTraitsState = (typeSlug, valueSlug) => {
    setTraitFilters((prevTraits) => {
      const newTraits = {
        ...prevTraits,
        [valueSlug]: prevTraits[valueSlug] ? undefined : typeSlug,
      };
      if (newTraits[valueSlug] === undefined) delete newTraits[valueSlug];
      return newTraits;
    });
  };

  return (
    <>
      {Object.entries(groupedTraits).map(([key, value]) => (
        <MPDropdown key={key} title={value.name} scrollableRef={bodyRef}>
          <ul className={joinClasses(CSSGlobal.Flex.Col, styles.traitsFilter)}>
            {value.items.map((item) => (
              <li key={`${item.typeSlug}-${item.valueSlug}`}>
                <MPCheckbox
                  label={
                    <div className={joinClasses(CSSGlobal.Flex.Row, CSSGap[8])}>
                      <div
                        className={joinClasses(
                          MPFonts.textSmallRegular,
                          styles.traitsTextContainer,
                          CSSGlobal.Ellipsis
                        )}
                      >
                        {item.value}
                      </div>
                      <div
                        className={joinClasses(
                          MPFonts.textSmallMedium,
                          MPColorClass.SolidNeutralGray3,
                          styles.traitsRarityContainer,
                          CSSGlobal.Ellipsis
                        )}
                      >
                        {item.count}
                      </div>
                    </div>
                  }
                  isChecked={traitsApplied[item.valueSlug] !== undefined}
                  onChange={() => {
                    updateTraitsState(item.typeSlug, item.valueSlug);
                  }}
                  name={item.valueSlug}
                />
              </li>
            ))}
          </ul>
        </MPDropdown>
      ))}
    </>
  );
}

export default TraitFilter;
