import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import ROUTES from 'constants/Routes';

import {
  ACTION_SVG_MAP,
  capitalizeFirstLetter,
  formatUserName,
  timePassedSinceDate,
  toPriceDisplay,
} from './ActivityUtils';

import * as styles from 'css/pages/profiles/activity/activity.module.css';

export default function ActivityTable({ rowData }) {
  return (
    <div className={styles.tableXAxisScroll}>
      <table className={styles.activityTable}>
        <thead className={styles.tableColName}>
          <tr>
            <th className={joinClasses(styles.columnHeader, styles.colItem)}>
              Item
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colFrom)}>
              From
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colTo)}>
              To
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colEdition)}>
              Edition
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colTime)}>
              Time
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colPrice)}>
              Price
            </th>
            <th className={joinClasses(styles.columnHeader, styles.colAction)}>
              Action
            </th>
          </tr>
        </thead>
        <tbody>
          {rowData.map((row, index) => (
            <tr
              key={row.id}
              style={{ background: index % 2 ? '#f8f8f8' : '#fff' }}
            >
              <td
                className={joinClasses(
                  styles.tableCell,
                  styles.tableItem,
                  MPFonts.bodyMedium,
                  styles.tableRow,
                  styles.tableCellPadding
                )}
              >
                <a
                  href={ROUTES.NFT(row.digitalMediaRelease.listing.productSlug)}
                  target="_blank"
                  rel="noreferrer"
                >
                  <img
                    className={styles.thumbnail}
                    src={row.digitalMediaRelease.metadata.thumbnailImage}
                  />
                </a>
                <a
                  href={ROUTES.NFT(row.digitalMediaRelease.listing.productSlug)}
                  target="_blank"
                  className={styles.textAnchor}
                  rel="noreferrer"
                >
                  {row.digitalMediaRelease.metadata.title}
                </a>
              </td>
              <td className={styles.tableCellPadding}>
                <a
                  className={joinClasses(styles.tableCell, styles.textAnchor)}
                  target="_blank"
                  href={
                    row.fromUser?.username
                      ? ROUTES.PROFILE(row.fromUser.username)
                      : ''
                  }
                  rel="noreferrer"
                >
                  {formatUserName(
                    row?.fromUser?.fullName || row?.fromUser?.username
                  )}
                </a>
              </td>
              <td className={styles.tableCellPadding}>
                <a
                  className={joinClasses(styles.tableCell, styles.textAnchor)}
                  target="_blank"
                  href={
                    row.toUser?.username
                      ? ROUTES.PROFILE(row.toUser.username)
                      : ''
                  }
                  rel="noreferrer"
                >
                  {formatUserName(
                    row?.toUser?.fullName || row?.toUser?.username
                  )}
                </a>
              </td>
              <td className={styles.tableCellPadding}>
                <span
                  className={joinClasses(styles.tableCell, styles.textRegular)}
                >
                  {row.digitalMediaRelease.printEdition}
                </span>
              </td>
              <td
                className={joinClasses(
                  styles.textRegular,
                  styles.tableCellPadding
                )}
              >
                {timePassedSinceDate(row.createdAt)}
              </td>
              <td
                className={joinClasses(
                  styles.textRegular,
                  styles.tableCellPadding
                )}
              >
                {toPriceDisplay(row)}
              </td>
              <td
                className={joinClasses(
                  styles.textRegular,
                  styles.tableCellPadding
                )}
              >
                {capitalizeFirstLetter(row.action)}&nbsp;
                <img
                  src={ACTION_SVG_MAP[row.action.toLowerCase()]}
                  className={styles.actionIcon}
                />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
