import { useEffect, useRef, useState } from 'react';
import pluralize from 'pluralize';
import { useRecoilValue } from 'recoil';

import { MPFonts } from '@mp-frontend/core-components';
import { TimerFilledIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import {
  AuctionType,
  ProductBidType,
  ProductPurchaseHistoryType,
  ProductType,
  RaffleType,
} from 'types/__generated__/graphql';

import NullIfEmptyDiv from 'components/NullIfEmptyComponent/NullIfEmptyDiv';
import SelectedCurrencyDisplayMode from 'state/SelectedCurrencyDisplayMode';
import CurrencyDisplayMode from 'types/enums/CurrencyDisplayMode';
import { Spread } from 'types/Spread';
import generatePriceString from 'utils/currency/generatePricing';
import dayjs from 'utils/datetime/dayjsex';

import * as styles from 'css/components/bar/DigitalMediaItemBar.module.css';

enum RaffleState {
  ENDED = 'ENDED',
  NOT_STARTED = 'NOT_STARTED',
  STARTED = 'STARTED',
}

const attributeTextClasses = joinClasses('reset', MPFonts.attributeText);
const attributeNumberClasses = joinClasses('reset', MPFonts.attributeNumber);

// eslint-disable-next-line @cspell/spellchecker
interface RaffleUpsellProductProp {
  raffle?: Pick<RaffleType, 'endsAt' | 'startsAt'>;
}

// eslint-disable-next-line @cspell/spellchecker
function RaffleUpsell({ product }: { product: RaffleUpsellProductProp }) {
  const [raffleState, setRaffleState] = useState(RaffleState.NOT_STARTED);
  useEffect(() => {
    if (product.raffle) {
      let startRaffleTimer;
      let endRaffleTimer;
      const raffleEnd = new Date(product.raffle.endsAt);
      const raffleStart = new Date(product.raffle.startsAt);
      const now = new Date();
      if (raffleEnd > now) {
        if (raffleStart < now) {
          setRaffleState(RaffleState.STARTED);
        } else {
          startRaffleTimer = setTimeout(
            () => setRaffleState(RaffleState.STARTED),
            now.getTime() - raffleStart.getTime()
          );
        }
        endRaffleTimer = setTimeout(
          () => setRaffleState(RaffleState.ENDED),
          raffleEnd.getTime() - now.getTime()
        );
      } else {
        setRaffleState(RaffleState.ENDED);
      }
      return () => {
        clearTimeout(startRaffleTimer);
        clearTimeout(endRaffleTimer);
      };
    }
    return undefined;
  }, [product, raffleState]);

  return raffleState === RaffleState.STARTED ? (
    <p className={attributeTextClasses}>Accepting Raffle Entries</p>
  ) : null;
}

type LowestAskProps = Pick<ProductType, 'lowestAskInEth' | 'lowestAskInUsd'>;

// Reserve
function LowestAsk({ product }: { product: LowestAskProps }) {
  // TODO (brian): implement this
  const isMobile = false;
  const currencyDisplayMode = useRecoilValue(SelectedCurrencyDisplayMode);
  const lowestAsk =
    currencyDisplayMode === CurrencyDisplayMode.ETH
      ? product.lowestAskInEth
      : product.lowestAskInUsd;
  const lowestAskStr = generatePriceString(lowestAsk, currencyDisplayMode, {
    isMobile,
    useUserCurrency: true,
  });

  // TODO(brian): Figure out how we get a lowestAskStr of length 0 and so on
  return lowestAsk > 0 && lowestAskStr.length > 0 ? (
    <>
      <p className={attributeTextClasses}>Lowest Ask</p>
      <p className={attributeNumberClasses}>{lowestAskStr}</p>
    </>
  ) : null;
}

function AuctionEnding({ endTimestamp, auctionEndCallback }: any) {
  // TODO(brian): Figure out a better pattern for this, or cleanup
  const [, forceUpdate] = useState({});
  const timerRef = useRef(0);

  useEffect(() => {
    timerRef.current = window.setInterval(() => forceUpdate({}), 1000);

    return () => {
      clearInterval(timerRef.current);
    };
  }, [endTimestamp]);

  const endsAt = dayjs(endTimestamp);
  const diff = endsAt.diff(dayjs());

  useEffect(() => {
    if (diff < 0) auctionEndCallback();
  });

  const greaterThan24hDiff = diff > 86400000; // in ms
  const duration = dayjs.duration(diff);
  const durationDays = Math.floor(duration.asDays());

  return (
    <>
      <p className={attributeTextClasses}>
        <span className={styles.timerWrapper}>
          <TimerFilledIcon fontSize="16px" />
        </span>
        {`Auction ${
          greaterThan24hDiff
            ? `ending in ${durationDays} ${pluralize('day', durationDays)}`
            : 'Ending'
        }`}
      </p>
      {!greaterThan24hDiff && (
        <p className={attributeNumberClasses}>
          <span className={styles.timerWrapper}>
            <TimerFilledIcon fontSize="16px" />
          </span>
          {duration.hours() > 0 ? `${duration.hours()}h ` : null}
          {duration.minutes() > 0 ? `${duration.minutes()}m ` : null}
          {duration.seconds() > 0 ? `${duration.seconds()}s ` : null}
        </p>
      )}
    </>
  );
}

const enum _AuctionState {
  ENDED = 'ENDED',
  NOT_STARTED = 'NOT_STARTED',
  STARTED = 'STARTED',
}

interface AuctionProductProp {
  auction?: Pick<AuctionType, 'auctionEnded' | 'endsAt'>;
}

function Auction({ product }: { product: AuctionProductProp }) {
  const { auction } = product;
  const [auctionState, setAuctionState] = useState(
    !auction
      ? _AuctionState.NOT_STARTED
      : auction.auctionEnded
      ? _AuctionState.ENDED
      : _AuctionState.STARTED
  );

  return auctionState === _AuctionState.STARTED ? (
    <AuctionEnding
      endTimestamp={auction.endsAt}
      auctionEndCallback={() => setAuctionState(_AuctionState.ENDED)}
    />
  ) : auctionState === _AuctionState.ENDED ? (
    <p className={attributeTextClasses}>Auction ended, pending sale</p>
  ) : null;
}

interface MarketCompProductProp {
  lastProductSale?: {
    ethPriceAtSale: ProductPurchaseHistoryType['ethPriceAtSale'];
    usdPriceAtSale: ProductPurchaseHistoryType['usdPriceAtSale'];
  };
  liveBid?: Pick<ProductBidType, 'bidInEther' | 'bidInUsd'>;
}

function MarketComp({ product }: { product: MarketCompProductProp }) {
  // TODO (brian): implement this
  const isMobile = false;

  const currencyDisplayMode = useRecoilValue(SelectedCurrencyDisplayMode);
  const displayModeIsCrypto = currencyDisplayMode === CurrencyDisplayMode.ETH;

  const offer = displayModeIsCrypto
    ? product.liveBid?.bidInEther
    : product.liveBid?.bidInUsd;
  const offerStr = generatePriceString(offer, currencyDisplayMode, {
    isMobile,
    useUserCurrency: true,
  });

  const lastSale = displayModeIsCrypto
    ? product.lastProductSale?.ethPriceAtSale ?? 0
    : product.lastProductSale?.usdPriceAtSale ?? 0;
  const lastSaleStr = generatePriceString(lastSale, currencyDisplayMode, {
    isMobile,
    useUserCurrency: true,
  });

  return product.liveBid && offer > 0 && offerStr.length > 0 ? (
    <>
      <p className={attributeTextClasses}>Highest Offer</p>
      <p className={attributeNumberClasses}>{offerStr}</p>
    </>
  ) : lastSale > 0 && lastSaleStr.length > 0 ? (
    <>
      <p className={attributeTextClasses}>Last Sale</p>
      <p className={attributeNumberClasses}>{lastSaleStr}</p>
    </>
  ) : null;
}

interface IProps {
  product: Spread<
    [
      AuctionProductProp,
      RaffleUpsellProductProp, // eslint-disable-line @cspell/spellchecker
      LowestAskProps,
      MarketCompProductProp
    ]
  >;
}

function Pricing({ product }: IProps) {
  return (
    <div className={styles.pricingInfo}>
      <NullIfEmptyDiv>
        <Auction product={product} />
        <RaffleUpsell product={product} />
        {!product.auction && <LowestAsk product={product} />}
      </NullIfEmptyDiv>
      <NullIfEmptyDiv className={styles.rightAlignHack}>
        <MarketComp product={product} />
      </NullIfEmptyDiv>
    </div>
  );
}

export default Pricing;
