import { useOutletContext, useParams } from 'react-router-dom';

import { MPTabsVariant } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import { ProfileAccountType } from 'types/__generated__/graphql';

import Tabs, { TabPassedProps, TabsProps } from 'components/Tabs';
import ROUTES from 'constants/Routes';
import CSSGlobal from 'types/enums/css/Global';
import withShopOrActivityCheck from 'utils/hocs/withProfileShopActivityTab';

import useProfileData, { useProfileDataQueryRef } from './useProfileData';

import * as styles from 'css/pages/profiles/ProfilePageTabs.module.css';

const TabSx = { minHeight: '36px', minWidth: '32px', padding: 0 };

function ProfilePageTabs({ currentTab }: { currentTab: string }) {
  const { profileSlug } = useParams();
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);

  const tabs: TabsProps<
    Record<string, unknown>,
    TabPassedProps & { reloadDocument?: boolean }
  >['tabs'] = [
    {
      label: 'Shop',
      sx: TabSx,
      to: ROUTES.PROFILE.SHOP(profileSlug),
      value: '/:profileslug/shop',
    },
  ];
  if (profileData.accountType !== ProfileAccountType.Collection)
    tabs.push({
      label: 'Gallery',
      sx: TabSx,
      to: `/${profileSlug}/gallery/`,
      value: '/:profileslug/gallery',
    });
  tabs.push({
    label: 'Activity',
    sx: TabSx,
    to: ROUTES.PROFILE.ACTIVITY(profileSlug),
    value: '/:profileslug/activity',
  });
  if (profileData.hasLikes) {
    tabs.push({
      label: 'Likes',
      sx: TabSx,
      to: `/${profileSlug}/likes`,
      value: '/:profileslug/likes',
    });
  }
  if (profileData.description) {
    tabs.push({
      label: 'About',
      sx: TabSx,
      to: `/${profileSlug}/about`,
      value: '/:profileslug/about',
    });
  }

  return (
    <>
      <div className={joinClasses(CSSGlobal.Flex.Row, styles.subpageTabs)}>
        <Tabs
          currentTab={currentTab}
          design={MPTabsVariant.Text}
          tabs={tabs}
          tabsSx={{ minHeight: '36px' }}
        />
      </div>
    </>
  );
}

export default withShopOrActivityCheck(ProfilePageTabs);
