import { useCallback, useEffect, useRef, useState } from 'react';

import * as styles from 'css/components/DigitalMediaVideo.module.css';

function DigitalMediaVideo({
  videoHovered,
  mediaContent,
  onVideoLoaded,
  loaded,
  media500xPreviewContent,
}) {
  const vidRef = useRef(null);
  const [isPlaying, setIsPlaying] = useState(false);

  const videoNode = useCallback(
    (node) => {
      if (node) {
        vidRef.current = node;
        if (node.readyState === 4) onVideoLoaded();
      }
    },
    [onVideoLoaded]
  );

  // If the video is hovered and the content is ready, play it
  useEffect(() => {
    if (vidRef.current && videoHovered && loaded) {
      setIsPlaying(true);
      vidRef.current.play();
    }
  }, [videoHovered, vidRef, loaded]);

  return (
    <div
      className={styles.digitalMediaVideoWrapper}
      style={{ backgroundImage: `url(${media500xPreviewContent})` }}
    >
      <video
        className={styles.idDigitalMediaVideo}
        ref={videoNode}
        loop
        muted
        playsInline
        style={{ visibility: isPlaying ? 'visible' : 'hidden' }}
        onCanPlay={onVideoLoaded}
        onCanPlayThrough={onVideoLoaded}
        onError={onVideoLoaded}
      >
        <source src={mediaContent} type="video/mp4" />
      </video>
    </div>
  );
}

export default DigitalMediaVideo;
