import { memo, useEffect, useState } from 'react';
import { debounce } from 'lodash';
import { useInView } from 'react-intersection-observer';

import {
  MPColorValue,
  MPTooltip,
  useIsMobile,
} from '@mp-frontend/core-components';
import { UnlockableIcon, VideoIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import ConditionalWrapper from 'components/ConditionalWrapper';
import useLinkWrapper from 'components/ConditionalWrapper/link';
import useDnd from 'hooks/useDnd';
import DragTypes from 'types/enums/DragTypes';
import { Spread } from 'types/Spread';

import DigitalMediaItemBar, {
  DigitalMediaItemBarProps,
} from './bar/DigitalMediaItemBar';
import DigitalMediaImage from './DigitalMediaImage';
import DigitalMediaVideo from './DigitalMediaVideo';

import * as styles from 'css/components/DigitalMediaItem.module.css';

export interface IItemContent {
  hasVideo: boolean;
  metadata: {
    height: number;
    id: string;
    width: number;
  };
  title: string;
  totalSupply: number;
  href?: string;
  media500xPreviewContent?: string;
  media1000xPreviewContent?: string;
  videoUrl?: string | null;
}

export interface DigitalMediaProps {
  dm: Spread<[DigitalMediaItemBarProps['dm'], IItemContent]>;
  product: any;
  release: {
    printEdition: number;
  };
  canDrag?: boolean;
  className?: string;
  dropCallback?: () => void;
  hoverCallback?: (_a: unknown, _b: unknown) => void;
  link?: string;
  showInfoBar?: boolean;
}

type NonNullableProps<T extends Record<string, unknown>> = {
  [P in keyof T]: NonNullable<T[P]>;
};
type NonNullBy<T, K extends keyof T> = Omit<T, K> &
  NonNullableProps<Required<Pick<T, K>>>;

type ItemContentWithVideoType = NonNullBy<IItemContent, 'videoUrl'>;

function itemHasVideo(dm: IItemContent): dm is ItemContentWithVideoType {
  return !!dm.hasVideo;
}

/*
  This component is going to load the preview image by default.
  If the item content is video then when the video is loaded, it will replace the preview image.

  This component will also control the playing state of the video itself by hover into it will trigger video.play
  and hide the video icon
*/
const DigitalMediaItem = memo(
  ({
    dm,
    release,
    product,
    link,
    className,
    hoverCallback,
    dropCallback,
    canDrag = false,
    showInfoBar = true,
  }: DigitalMediaProps) => {
    const {
      hasVideo,
      media1000xPreviewContent,
      media500xPreviewContent,
      metadata,
    } = dm;

    const [videoLoaded, setVideoLoaded] = useState(false); // Telling whether the video has finished loading or not
    const [videoHovered, setVideoHovered] = useState(false);
    const [imageLoaded] = useState(false);
    const [contentLoaded, setContentLoaded] = useState(false);
    const [showPlayIcon, setShouldVideoIconDisplay] = useState(false);
    const linkWrapper = useLinkWrapper(link);

    const { ref: inViewRef, inView } = useInView({
      fallbackInView: true,
      root: null,
      triggerOnce: true,
    });

    const [{ style: dragStyle }, dndRef] = useDnd(
      release,
      canDrag,
      DragTypes.DigitalMediaItem,
      hoverCallback,
      dropCallback,
      inViewRef
    );

    const playVideo = (shouldPlay: boolean) => {
      if (shouldPlay) {
        setVideoHovered(true);
      }
    };

    const delayedMouseEnterHandler = debounce(
      (shouldPlay) => playVideo(shouldPlay),
      250
    );

    const onMouseEnterHandler = () => {
      delayedMouseEnterHandler(true);
    };

    const onVideoLoaded = () => {
      setVideoLoaded(true);
    };

    useEffect(() => {
      // contentLoaded == either the image has been loaded or the video has been loaded
      setContentLoaded(imageLoaded || videoLoaded);
    }, [imageLoaded, videoLoaded]);

    useEffect(() => {
      // Only display the video icon if the item has a video and the video is not hovered
      setShouldVideoIconDisplay(!!hasVideo && !videoHovered && contentLoaded);
    }, [videoHovered, contentLoaded, hasVideo]);

    const isMobile = useIsMobile();

    return (
      <div key={media500xPreviewContent} ref={dndRef}>
        <div
          style={dragStyle}
          className={joinClasses(styles.idDigitalMediaItem, className)}
          onMouseEnter={onMouseEnterHandler}
        >
          {!!product.hasUnlockable && (
            <MPTooltip
              title="Unlockables are a category of NFTs which grant their owners special access to perks such as physical artworks, experiences, and more!"
              arrow
              placement="top"
            >
              <div className={styles.unlockableIconWrapper}>
                <UnlockableIcon className={styles.unlockableIcon} />
              </div>
            </MPTooltip>
          )}
          {!!showPlayIcon && (
            <VideoIcon
              className={styles.playIcon}
              htmlColor={MPColorValue.CommonWhite}
              alt="Play Video"
            />
          )}
          <ConditionalWrapper condition={!!link} wrapper={linkWrapper}>
            <>
              {!!inView && !itemHasVideo(dm) && (
                <DigitalMediaImage
                  metadata={metadata || { height: 'auto', width: 'auto' }}
                  media500xPreviewContent={media500xPreviewContent}
                  media1000xPreviewContent={media1000xPreviewContent}
                />
              )}
              {!!inView && itemHasVideo(dm) && (
                <DigitalMediaVideo
                  mediaContent={dm.videoUrl}
                  onVideoLoaded={onVideoLoaded}
                  loaded={videoLoaded}
                  videoHovered={videoHovered}
                  media500xPreviewContent={media500xPreviewContent}
                />
              )}
            </>
          </ConditionalWrapper>
          {!!showInfoBar && !isMobile && (
            <DigitalMediaItemBar
              className={styles.bar}
              dm={dm}
              printEdition={release.printEdition}
              href={link}
              product={product}
            />
          )}
        </div>
        {!!showInfoBar && !!isMobile && (
          <DigitalMediaItemBar
            className={styles.bar}
            dm={dm}
            printEdition={release.printEdition}
            href={link}
            product={product}
          />
        )}
      </div>
    );
  }
);

export default DigitalMediaItem;
