import { Outlet, useOutletContext } from 'react-router-dom';

import { ProfileAccountType } from 'types/__generated__/graphql';

import DefaultSuspense from 'components/DefaultSuspense';

import ProfilePageTabs from './ProfilePageTabs';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

function Gallery() {
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);

  return (
    <>
      <ProfilePageTabs currentTab="/:profileslug/gallery" />
      <DefaultSuspense>
        <Outlet
          context={{
            defaultRedirectSlug:
              profileData.accountType === ProfileAccountType.Creator ||
              profileData.accountType === ProfileAccountType.Collection ||
              profileData.accountType === ProfileAccountType.DowngradedCreator
                ? profileData.defaultCategory
                  ? `created/${profileData.defaultCategory.categorySlug}`
                  : 'created'
                : profileData.defaultCollection
                ? `collected/${profileData.defaultCollection.categorySlug}`
                : 'collected',
            profileQueryRef,
          }}
        />
      </DefaultSuspense>
    </>
  );
}

export default Gallery;
