import {
  Dispatch,
  MutableRefObject,
  SetStateAction,
  useCallback,
  useEffect,
  useMemo,
  useRef,
} from 'react';
import _ from 'lodash';
import { useOutletContext } from 'react-router-dom';

import { MPMasonry, useIsMobile } from '@mp-frontend/core-components';

import { GalleryProfile$data } from 'graphql/__generated__/GalleryProfile.graphql';
import { CategoryType } from 'types/__generated__/graphql';

import useInfiniteQueryScroll from 'hooks/useInfiniteQueryScroll';
import useGalleryCollectionPaginationFragment, {
  useGalleryCollectionPaginationFragmentQueryRef,
} from 'pages/profiles/useGalleryCollectionFragment';
import { Category } from 'types/graphql/Category';
import { GalleryDigitalMediaRelease } from 'types/graphql/Gallery';
import emptyFunc from 'utils/emptyFunc';

import useGalleryPaginationFragment, {
  useGalleryPaginationFragmentQueryRef,
} from '../../useGalleryPaginationFragment';
import DigitalMediaSelectionItem from './DigitalMediaSelectionItem';

interface IMasonryProps {
  categoryType: CategoryType;
  filter: string;
  mode: 'update' | 'create';
  paginationRef:
    | useGalleryCollectionPaginationFragmentQueryRef
    | useGalleryPaginationFragmentQueryRef;
  scrollRef: MutableRefObject<Element | null>;
  selected: Set<GalleryDigitalMediaRelease['liveProduct']['pk']>;
  setSelected: Dispatch<
    SetStateAction<Set<GalleryDigitalMediaRelease['liveProduct']['pk']>>
  >;
  category?: Category;
}

type Mutable<T> = {
  -readonly [P in keyof T]: Mutable<T[P]>;
};

export default function CategoryDialogMasonry({
  scrollRef,
  filter,
  categoryType,
  category,
  mode,
  paginationRef,
  selected,
  setSelected,
}: IMasonryProps) {
  const wasMarked = useRef(new Set());
  const addMarkedToSelected = useRef<Array<string>>([]);
  const isMobile = useIsMobile();

  useEffect(() => {
    if (addMarkedToSelected.current.length) {
      setSelected((prevSelected) => {
        const tempSet = new Set(prevSelected);
        addMarkedToSelected.current.forEach((mark) => tempSet.add(mark));
        return tempSet;
      });
      addMarkedToSelected.current.length = 0;
    }
  });

  const useGenerateData = useCallback(() => {
    /* eslint-disable react-hooks/rules-of-hooks */
    const { allGalleryRef } = useOutletContext<{
      allGalleryRef:
        | useGalleryCollectionPaginationFragmentQueryRef
        | useGalleryPaginationFragmentQueryRef;
    }>();
    const allGallery =
      categoryType === CategoryType.Collection
        ? useGalleryCollectionPaginationFragment(
            allGalleryRef as useGalleryCollectionPaginationFragmentQueryRef
          )
        : useGalleryPaginationFragment(
            allGalleryRef as useGalleryPaginationFragmentQueryRef
          );
    const catGallery =
      categoryType === CategoryType.Collection
        ? useGalleryCollectionPaginationFragment(
            paginationRef as useGalleryCollectionPaginationFragmentQueryRef
          )
        : useGalleryPaginationFragment(
            paginationRef as useGalleryPaginationFragmentQueryRef
          );
    /* eslint-enable react-hooks/rules-of-hooks */

    if (mode === 'update' && catGallery.hasNext && catGallery.loadNext)
      catGallery.loadNext(9999999);

    if (!category)
      return (
        allGallery || {
          data: [],
          hasNext: true,
          isLoadingNext: true,
          loadNext: emptyFunc,
        }
      );

    // loDash needs to update their typings
    const data = _.cloneDeep(catGallery.data) as Mutable<GalleryProfile$data>;
    const allData = _.cloneDeep(
      allGallery.data
    ) as Mutable<GalleryProfile$data>;

    if (data?.results?.edges) {
      data.results.edges.forEach((edge) => {
        if (edge?.node?.latestRelease.liveProduct.pk) {
          const id = edge?.node?.latestRelease.liveProduct.pk;
          if (wasMarked.current.has(id)) return;
          wasMarked.current.add(id);
          addMarkedToSelected.current.push(id);
        }
      });
    }

    if (
      !catGallery.hasNext &&
      data?.results?.edges &&
      allData?.results?.edges
    ) {
      const catEdges = data.results.edges;
      const allEdges = allData?.results?.edges;
      const edges = allEdges.filter(
        (edge) =>
          !catEdges.find(
            (catEdge) =>
              catEdge?.node?.latestRelease.liveProduct.id ===
              edge?.node?.latestRelease.liveProduct.id
          )
      );
      data.results.edges = data.results.edges.concat(edges);
    }

    return {
      data,
      hasNext: allGallery.hasNext || catGallery.hasNext,
      isLoadingNext: allGallery.isLoadingNext || catGallery.isLoadingNext,
      loadNext: catGallery.hasNext ? catGallery.loadNext : allGallery.loadNext,
    };
  }, [category, paginationRef, mode, categoryType]);

  const { data: oData } = useInfiniteQueryScroll({
    pageSize: 10,
    paginatedQueryResults: useGenerateData(),
    ref: scrollRef,
    scrollRef,
  });

  const data: Array<GalleryDigitalMediaRelease> = oData.map(
    (dm) => dm.latestRelease
  );

  const masonryData = useMemo(
    () =>
      data
        ? data.map((release) => ({
            isSelected: selected.has(release.liveProduct.pk),
            release,
          }))
        : [],
    [data, selected]
  );

  const clickCallback = (release: GalleryDigitalMediaRelease) => {
    setSelected((prevSelected) => {
      const tempSet = new Set(prevSelected);
      tempSet.has(release.liveProduct.pk)
        ? tempSet.delete(release.liveProduct.pk)
        : tempSet.add(release.liveProduct.pk);
      return tempSet;
    });
  };

  const filteredMasonryData = useMemo(() => {
    const searchTerm = filter.toLowerCase();
    return masonryData.filter(
      (item) =>
        item.release.digitalMedia.title.toLowerCase().includes(searchTerm) ||
        item.release.digitalMedia.description.toLowerCase().includes(searchTerm)
    );
  }, [filter, masonryData]);

  return filteredMasonryData.length ? (
    <MPMasonry columns={isMobile ? 2 : 3} spacing={2}>
      {filteredMasonryData.map((item) => (
        <DigitalMediaSelectionItem
          key={item.release.id}
          item={item}
          clickCallback={clickCallback}
        />
      ))}
    </MPMasonry>
  ) : null;
}
