import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import generateEditionString from 'utils/generateEditionText';

import * as styles from 'css/components/bar/DigitalMediaItemBar.module.css';

interface IProps {
  href: string;
  printEdition: number;
  totalSupply: number;
  title?: string;
}

function Header({ href, totalSupply, printEdition, title = '' }: IProps) {
  return (
    <div className={styles.header}>
      <a
        href={href}
        className="invisibleAnchor"
        target="_blank"
        rel="noreferrer"
      >
        <p className={joinClasses('reset', MPFonts.attributeTitle)}>{title}</p>
      </a>
      <div className={styles.editionInfo}>
        <p className={joinClasses('reset', MPFonts.attributeEdition)}>
          {generateEditionString(printEdition, totalSupply)}
        </p>
      </div>
    </div>
  );
}

export default Header;
