import { useCallback, useMemo } from 'react';
import { usePreloadedQuery } from 'react-relay';

import { MPActionButton, useIsMobile } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import ProfilesCollectionTraitsQueryType, {
  ProfilesCollectionTraitsQuery,
} from 'graphql/__generated__/ProfilesCollectionTraitsQuery.graphql';
import { StatusEnum } from 'types/__generated__/graphql';

import FilterSortButton from 'pages/explore/artwork/FilterSortButton';
import useArtworkFilterState from 'pages/explore/artwork/UseArtworkFilterState';
import { Genres } from 'pages/explore/genre';
import CSSGlobal from 'types/enums/css/Global';
import CSSMargin from 'types/enums/css/Margin';
import { NFTTraitsType } from 'types/graphql/NFT';
import withDefaultErrorBoundary from 'utils/hocs/withDefaultErrorBoundary';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';

import PriceFilter from './filters/PriceFilter';
import TraitFilter from './filters/TraitFilters';
import { FiltersGroup, FiltersSection } from './filters';
import ShopChips from './ShopChips';

interface ShopCollectionFiltersProps {
  profilesCollectionTraitsQuery: WithLoadQueryProps<ProfilesCollectionTraitsQuery>;
}

function ShopCollectionFilters({
  profilesCollectionTraitsQuery,
}: ShopCollectionFiltersProps) {
  const isMobile = useIsMobile();
  const filterParams = useArtworkFilterState();
  const { status } = filterParams;
  const [selectedFilterStatus, setFilterStatus] = status;
  const { nftCollections } = usePreloadedQuery<ProfilesCollectionTraitsQuery>(
    ProfilesCollectionTraitsQueryType,
    profilesCollectionTraitsQuery.queryRef
  );
  const shouldShowSortButton = !(
    !!filterParams.query[0] || filterParams.genre[0]() !== Genres.All
  );

  const isListedForSaleSelected = !!selectedFilterStatus[StatusEnum.BuyNow];

  const handleClickListedForSale = useCallback(() => {
    setFilterStatus((prevStatus) => ({
      ...prevStatus,
      [StatusEnum.BuyNow]: !isListedForSaleSelected || undefined,
    }));
  }, [isListedForSaleSelected, setFilterStatus]);

  const traits = useMemo(
    () =>
      nftCollections.edges.length > 0
        ? nftCollections.edges[0].node.traits
        : [],
    [nftCollections.edges]
  ) as NFTTraitsType[];

  const groupByTypeSlug = (): Record<
    string,
    { items: NFTTraitsType[]; name: string }
  > =>
    traits
      ?.filter((trait) => trait.valueSlug !== '')
      .reduce((acc, item) => {
        if (!acc[item.typeSlug]) {
          acc[item.typeSlug] = { items: [], name: item.type };
        }
        acc[item.typeSlug].items.push(item);
        return acc;
      }, {} as Record<string, { items: NFTTraitsType[]; name: string }>);

  return (
    <div className={joinClasses(CSSMargin.TOP[24], CSSMargin.BOTTOM[24])}>
      <FiltersSection inline={!isMobile}>
        {!!shouldShowSortButton && (
          <>
            <FiltersSection inline scrollable={isMobile}>
              <FiltersGroup title="Sorting">
                <FilterSortButton align="left" />
              </FiltersGroup>
              <FiltersGroup title="Filters">
                <MPActionButton
                  variant={
                    isListedForSaleSelected ? 'secondary-black' : 'secondary'
                  }
                  className={CSSGlobal.Width.maxContent}
                  onClick={handleClickListedForSale}
                  size="small"
                >
                  Listed for Sale
                </MPActionButton>
                <PriceFilter />
              </FiltersGroup>
            </FiltersSection>
            {traits?.length > 0 && (
              <FiltersSection inline scrollable={isMobile}>
                <FiltersGroup title="Attributes">
                  <TraitFilter groupedTraits={groupByTypeSlug()} />
                </FiltersGroup>
              </FiltersSection>
            )}
          </>
        )}
      </FiltersSection>
      <ShopChips collectionTraits={traits} />
    </div>
  );
}

export default withDefaultErrorBoundary(
  withLoadQuery(ShopCollectionFilters, {
    profilesCollectionTraitsQuery: {
      concreteRequest: ProfilesCollectionTraitsQueryType,
    },
  }),
  { hideState: true }
);
