import {
  PreloadedQuery,
  usePaginationFragment,
  usePreloadedQuery,
} from 'react-relay';

import GalleryCollectionProfileData, {
  GalleryCollectionProfile$key,
} from 'graphql/__generated__/GalleryCollectionProfile.graphql';
import GetCollectionGallery, {
  GalleryCollectionQuery,
} from 'graphql/__generated__/GalleryCollectionQuery.graphql';

export type useGalleryCollectionPaginationFragmentQueryRef =
  PreloadedQuery<GalleryCollectionQuery>;

// This hook as been split off to make it easier to reused for the category Dialog.
export default function useGalleryCollectionPaginationFragment(
  queryRef: useGalleryCollectionPaginationFragmentQueryRef
) {
  return usePaginationFragment<
    GalleryCollectionQuery,
    GalleryCollectionProfile$key
  >(
    GalleryCollectionProfileData,
    usePreloadedQuery<GalleryCollectionQuery>(GetCollectionGallery, queryRef)
  );
}
