import { useOutletContext, useParams } from 'react-router-dom';
import { useGate } from 'statsig-react';

import { MPFonts } from '@mp-frontend/core-components';

import ActivityPaginatedQueryType, {
  ActivityPaginatedQuery,
} from 'graphql/__generated__/ActivityPaginatedQuery.graphql';

import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import { ACTIVITY_PER_PAGE } from 'pages/activity/ActivityPage';
import { ActivityPageTable } from 'pages/activity/ActivityPageTable';
import withLoadQuery, { WithLoadQueryProps } from 'utils/hocs/withLoadQuery';

import ActivityFilterSortButton from './activity/ActivityFilterSortButton';
import ActivityGrid from './activity/ActivityGrid';
import useActivityFilterState from './activity/UseActivityFilterState';
import ProfilePageTabs from './ProfilePageTabs';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

import * as styles from 'css/pages/profiles/activity/activity.module.css';

function NoResults() {
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);

  return (
    <div className="centerText">
      <h2 className={MPFonts.headline4}>
        {`${profileData.displayName} doesn't have any activities at this time`}
      </h2>
    </div>
  );
}

const ActivityTable = withLoadQuery(
  ({
    activityQuery,
  }: {
    activityQuery: WithLoadQueryProps<ActivityPaginatedQuery>;
  }) => (
    <ActivityPageTable
      activityQuery={activityQuery}
      className={styles.activities}
      showCollector
    />
  ),
  {
    activityQuery: { concreteRequest: ActivityPaginatedQueryType },
  }
);

function ActivityPage() {
  const gate = useGate('gate_profile_new_activity');
  const showNewActivity = !gate.isLoading && gate.value;

  const { profileSlug } = useParams();
  const filterParams = useActivityFilterState();
  const activityQueryVariables: ActivityPaginatedQuery['variables'] = {
    currencyMode: filterParams.currencyMode[0],
    first: ACTIVITY_PER_PAGE,
    sortOrder: filterParams.sortOrder[0],
    sortType: filterParams.sortType[0],
    storeCollectionSlug: profileSlug,
  };

  return (
    <>
      <ProfilePageTabs currentTab="/:profileslug/activity" />
      {!showNewActivity ? (
        <DefaultErrorBoundary>
          <ActivityFilterSortButton />
          <div className={styles.nftGridMargin} />
          <ActivityGrid artist={profileSlug} noResults={NoResults} />
        </DefaultErrorBoundary>
      ) : (
        <DefaultErrorBoundary>
          <ActivityFilterSortButton />

          <ActivityTable
            activityQuery={{ variables: activityQueryVariables }}
          />
        </DefaultErrorBoundary>
      )}
    </>
  );
}

export default ActivityPage;
