import { Children } from 'react';

const isChildNull = (children) => {
  const { type, props } = children;
  // This could be a performance issue, since we'll be processing each node twice
  return type(props) === null;
};

function NullIfEmptyDiv({ children, ...props }) {
  const nonNullChildren = Children.toArray(children).filter(
    (child) => !isChildNull(child)
  );
  return nonNullChildren.length ? <div {...props}>{children}</div> : null;
}

export default NullIfEmptyDiv;
