import { lazy } from 'react';
import { Navigate, Route, Routes, useParams } from 'react-router-dom';

import { CategoryType } from 'types/__generated__/graphql';

import PageErrorBoundary from 'components/ErrorBoundaries/PageErrorBoundary';
import Reload from 'components/Reload';
import ROUTES from 'constants/Routes';

import CategoryGallery from './gallery/CategoryGallery';
import Collected from './gallery/Collected';
import Created from './gallery/Created';
import About from './About';
import Activity from './Activity';
import Gallery from './Gallery';
import Likes from './Likes';
import Redirect from './Redirect';
import Shop from './Shop';

const ProfilePage = lazy(() => import('./ProfilePage'));

function RedirectToCollection() {
  const { profileSlug } = useParams();
  return (
    <Navigate to={ROUTES.PROFILE.GALLERY.COLLECTED(profileSlug)} replace />
  );
}

export default function ProfilesRefreshRouteRoute() {
  return (
    <Routes>
      <Route
        path=":profileSlug"
        element={
          <PageErrorBoundary>
            <ProfilePage />
          </PageErrorBoundary>
        }
      >
        <Route index element={<Redirect />} />
        <Route path="shop" element={<Shop />} />
        <Route path="activity" element={<Activity />} />
        <Route path="likes" element={<Likes />} />
        <Route path="gallery" element={<Gallery />}>
          <Route index element={<Redirect />} />
          <Route path="created" element={<Created />}>
            <Route
              path=":categorySlug"
              element={<CategoryGallery type={CategoryType.Store} />}
            />
            <Route
              index
              element={<CategoryGallery type={CategoryType.Store} />}
            />
          </Route>
          <Route path="collected" element={<Collected />}>
            <Route
              path=":categorySlug"
              element={<CategoryGallery type={CategoryType.Collection} />}
            />
            <Route
              index
              element={<CategoryGallery type={CategoryType.Collection} />}
            />
          </Route>
        </Route>
        <Route path="about" element={<About />} />
        <Route path="collection" element={<RedirectToCollection />} />
        <Route path="*" element={<Reload />} />
      </Route>
    </Routes>
  );
}
