import { useCallback, useEffect, useMemo, useState } from 'react';
import { PreloadedQuery, usePreloadedQuery } from 'react-relay';
import {
  Link,
  useNavigate,
  useOutletContext,
  useParams,
} from 'react-router-dom';

import {
  MPAnimations,
  MPColorClass,
  MPFonts,
  MPTabsVariant,
  useIsMobile,
} from '@mp-frontend/core-components';
import { joinClasses, useRefState } from '@mp-frontend/core-utils';

import GetCategories, {
  CategoryV3Query,
} from 'graphql/__generated__/CategoryV3Query.graphql';
import GetCollectionGallery, {
  GalleryCollectionQuery,
  GalleryCollectionQuery$variables,
} from 'graphql/__generated__/GalleryCollectionQuery.graphql';
import GetGallery, {
  GalleryQuery,
  GalleryQuery$variables,
} from 'graphql/__generated__/GalleryQuery.graphql';
import { CategoryType } from 'types/__generated__/graphql';

import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import Tabs, { TabPassedProps, TabsProps } from 'components/Tabs';
import DraggableTab, { DraggableTabProps } from 'components/Tabs/DraggableTab';
import LEGACY_API from 'constants/LegacyAPI';
import useLoadQuery from 'hooks/useLoadQuery';
import { Category } from 'types/graphql/Category';
import useOnEnterKey from 'utils/eventHandlers/useOnEnterKey';
import getToken from 'utils/getToken';

import CategoryDialog from '../dialog/EditCategory';
import { useGalleryPaginationFragmentQueryRef } from '../useGalleryPaginationFragment';
import useProfileData, { useProfileDataQueryRef } from '../useProfileData';
import CategoryMasonry from './CategoryMasonry';
import useBackingDndData from './useBackingDndData';

import * as styles from 'css/pages/profiles/gallery/CategoryGallery.module.css';

const TabSx = { minHeight: '36px', minWidth: '32px', padding: 0 };

interface ICategoryTabPanel {
  invalidateTabs: () => void;
  profileSlug: string;
  type: CategoryType;
  /* eslint-disable-next-line react/no-unused-prop-types */
  value: string;
  category?: Category;
  isCurrentUser?: boolean;
  navigateToAll?: () => void;
  navigateToNewCategory?: (category: Category) => void;
}

function CategoryTabPanel({
  type,
  profileSlug,
  category,
  navigateToNewCategory,
  navigateToAll,
  invalidateTabs,
  isCurrentUser,
}: ICategoryTabPanel) {
  const queryParams: GalleryQuery$variables | GalleryCollectionQuery$variables =
    {
      categorySlug: category?.categorySlug,
      collectorSlug: type === CategoryType.Collection ? profileSlug : undefined,
      // If its the user's profile, load all of the items to facilitate dnd
      first: 10,
      hiddenOnCreate: type === CategoryType.Store ? false : undefined,
      slug: type === CategoryType.Store ? profileSlug : undefined,
    };

  const [queryRef, , , invalidateGallery] = useLoadQuery<GalleryQuery>(
    type === CategoryType.Collection ? GetCollectionGallery : GetGallery,
    queryParams
  );

  const editCategoryCallback = useCallback(
    (_category: Category) => {
      invalidateGallery();
      navigateToNewCategory(_category);
    },
    [invalidateGallery, navigateToNewCategory]
  );

  const sx = useIsMobile() ? { margin: 0 } : {};

  return (
    <DefaultErrorBoundary>
      {!!queryRef && (
        <CategoryMasonry
          type={type}
          queryRef={
            queryRef as
              | PreloadedQuery<GalleryQuery>
              | PreloadedQuery<GalleryCollectionQuery>
          }
          profileSlug={profileSlug}
          category={category}
          sx={sx}
          editCategoryCallback={editCategoryCallback}
          navigateToAll={navigateToAll}
          isCurrentUser={isCurrentUser}
          invalidateTabs={invalidateTabs}
        />
      )}
    </DefaultErrorBoundary>
  );
}

interface ICategoryProps {
  type: CategoryType;
}

function CategoryGallery({ type }: ICategoryProps) {
  const [portalNode, portalRef] = useRefState<HTMLDivElement>();
  const isMobile = useIsMobile();

  const { profileQueryRef, categoryQueryRef, allGalleryRef } =
    useOutletContext<{
      allGalleryRef: useGalleryPaginationFragmentQueryRef;
      categoryQueryRef: PreloadedQuery<CategoryV3Query>;
      profileQueryRef: useProfileDataQueryRef;
    }>();
  const profileData = useProfileData(profileQueryRef);
  const categories = usePreloadedQuery<CategoryV3Query>(
    GetCategories,
    categoryQueryRef
  ).results;
  const { profileSlug, categorySlug = '' } = useParams();

  const baseValue = `/:profileSlug/gallery/${
    type === CategoryType.Store ? 'created' : 'collected'
  }`;
  const baseTo = `/${profileSlug}/gallery/${
    type === CategoryType.Store ? 'created' : 'collected'
  }`;

  const tabs: Array<
    {
      canDrag: boolean;
      category?: Category;
      slug?: string;
    } & TabsProps['tabs'][0]
  > = [
    {
      canDrag: false,
      label: 'All',
      slug: '',
      sx: TabSx,
      to: `${baseTo}`,
      value: `${baseValue}`,
    },
  ].concat(
    categories.items.map((category) => ({
      canDrag: profileData.isCurrentUser && !isMobile,
      category,
      label: category.categoryName,
      slug: category.categorySlug,
      sx: TabSx,
      to: `${baseTo}/${category.categorySlug}`,
      value: `${baseValue}/${category.categorySlug}`,
    }))
  );

  const [isCategoryDialogOpen, setCategoryDialogOpen] = useState(false);
  const openCategoryDialog = useCallback(
    () => setCategoryDialogOpen(true),
    [setCategoryDialogOpen]
  );
  const ariaOpenCategoryDialog = useOnEnterKey(openCategoryDialog);

  const navigate = useNavigate();
  const navigateToNewCategory = useCallback(
    (category: Category) => {
      navigate(`${baseTo}/${category.categorySlug}`);
    },
    [baseTo, navigate]
  );
  const navigateToAll = useCallback(() => {
    navigate(`${baseTo}`);
  }, [baseTo, navigate]);

  const navigateToDefault = useCallback(() => {
    const defCategory =
      type === CategoryType.Store
        ? profileData.defaultCategory
          ? `${baseTo}/${profileData.defaultCategory.categorySlug}`
          : `${baseTo}`
        : profileData.defaultCollection
        ? `${baseTo}/${profileData.defaultCollection.categorySlug}`
        : `${baseTo}`;
    navigate(defCategory);
  }, [
    baseTo,
    navigate,
    type,
    profileData.defaultCategory,
    profileData.defaultCollection,
  ]);

  useEffect(() => {
    if (
      categorySlug &&
      !categories.items.find(
        (category) => category.categorySlug === categorySlug
      )
    )
      navigateToDefault();
  }, [categorySlug, navigateToDefault, categories.items]);

  // We roll our own since useMemo is not a semantic guarantee
  const [derivedTabs, hoverCallback, invalidateTabs] = useBackingDndData(
    tabs,
    [],
    'value'
  );

  const dropCallback = useCallback(() => {
    const headers = new Headers();
    headers.append('X-CSRFToken', getToken());
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const body = new URLSearchParams();
    derivedTabs.forEach((tab) => {
      if (tab.category)
        body.append('category_ids[]', tab.category.pk.toString());
    });

    fetch(LEGACY_API.MUTATE_CATEGORY_ORDERING, {
      body,
      headers,
      method: 'POST',
    });
  }, [derivedTabs]);

  const tabComponent = useMemo(
    () => ({
      component: Link,
      props: {
        dropCallback,
        hoverCallback,
      },
      type: DraggableTab,
    }),
    [dropCallback, hoverCallback]
  );

  return (
    <>
      {!!(profileData.isCurrentUser || derivedTabs.length > 1) && (
        <div
          className={styles.tabsContainer}
          data-hascreatedcollected={profileData.hasCollectedDigitalMedias}
        >
          <Tabs<
            {
              dropCallback: DraggableTabProps['dropCallback'];
              hoverCallback: DraggableTabProps['hoverCallback'];
            },
            { canDrag: boolean } & TabPassedProps
          >
            currentTab={
              !categorySlug ? `${baseValue}` : `${baseValue}/${categorySlug}`
            }
            design={MPTabsVariant.Text}
            tabs={derivedTabs}
            TabComponent={tabComponent}
            panelContainerNode={portalNode}
            tabsSx={{ cursor: 'pointer', marginTop: '4px', minHeight: '36px' }}
          >
            {derivedTabs.map((tab) => (
              <CategoryTabPanel
                value={tab.value}
                key={tab.value}
                profileSlug={profileSlug}
                type={type}
                category={tab.category}
                navigateToNewCategory={navigateToNewCategory}
                navigateToAll={navigateToAll}
                invalidateTabs={invalidateTabs}
                isCurrentUser={profileData.isCurrentUser}
              />
            ))}
          </Tabs>
          {!!profileData.isCurrentUser && (
            <>
              <span
                className={joinClasses(
                  styles.createCategoryWrapper,
                  MPFonts.textNormalMedium,
                  MPColorClass.SolidNeutralGray5,
                  MPAnimations.Color.DarkToLight
                )}
                onClick={openCategoryDialog}
                onKeyPress={ariaOpenCategoryDialog}
                role="button"
                tabIndex={0}
              >
                Add&nbsp;New
              </span>
              <CategoryDialog
                categoryQueryRef={allGalleryRef}
                isOpen={isCategoryDialogOpen}
                setOpen={setCategoryDialogOpen}
                mode="create"
                defaultCategorySlug={
                  profileData.defaultCategory?.categorySlug ?? ''
                }
                defaultCollectionSlug={
                  profileData.defaultCollection?.categorySlug ?? ''
                }
                addCategoryCallback={navigateToNewCategory}
                invalidateTabs={invalidateTabs}
                categoryType={type}
              />
            </>
          )}
        </div>
      )}
      <div className={styles.tabPanelContainer} ref={portalRef}>
        {!profileData.isCurrentUser && tabs.length <= 1 && (
          <CategoryTabPanel
            value={baseValue}
            profileSlug={profileSlug}
            type={type}
            invalidateTabs={invalidateTabs}
          />
        )}
      </div>
    </>
  );
}

export default CategoryGallery;
