import { useEffect, useRef } from 'react';
import {
  PreloadedQuery,
  usePaginationFragment,
  usePreloadedQuery,
  useQueryLoader,
} from 'react-relay';
import { useOutletContext, useParams } from 'react-router-dom';
import { useMediaQuery, useTheme } from '@mui/material';

import { MPFonts, MPMasonry, useIsMobile } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import LikesAllProfilesData, {
  LikesAllProfile$key,
} from 'graphql/__generated__/LikesAllProfile.graphql';
import GetLikes, { LikesQuery } from 'graphql/__generated__/LikesQuery.graphql';

import { DefaultLoader } from 'components/DefaultSuspense';
import DigitalMediaItem from 'components/DigitalMediaItem/DigitalMediaItem';
import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import useInfiniteQueryScroll from 'hooks/useInfiniteQueryScroll';
import CSSGlobal from 'types/enums/css/Global';

import ProfilePageTabs from './ProfilePageTabs';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

import * as styles from 'css/pages/profiles/Likes.module.css';
import * as noContentStyles from 'css/pages/profiles/NoContent.module.css';

interface IProps {
  queryRef: PreloadedQuery<LikesQuery>;
}

function LikesInner({ queryRef }: IProps) {
  const visibilityRef = useRef(null);
  const scrollRef = useRef(null);
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);

  const {
    data: oData,
    hasMore,
    loading,
  } = useInfiniteQueryScroll({
    pageSize: 1,
    paginatedQueryResults: usePaginationFragment<
      LikesQuery,
      LikesAllProfile$key
    >(LikesAllProfilesData, usePreloadedQuery<LikesQuery>(GetLikes, queryRef)),
    ref: visibilityRef,
    scrollRef,
  });
  const data = oData.map((dm) => dm.latestRelease);

  const isExtraWide = useMediaQuery(useTheme().breakpoints.up('wide'));
  const isMobile = useIsMobile();
  const GalleryColumnNum = isExtraWide ? 4 : !isMobile ? 3 : 1;

  return (
    <div className={styles.likesGalleryContainer} ref={scrollRef}>
      {data.length === 0 && !hasMore && (
        <div
          className={joinClasses(
            CSSGlobal.Flex.CenteredCol,
            noContentStyles.noContent
          )}
        >
          <img width="128" src="/static/img/icons/empty_gallery.png" />
          <p
            className={joinClasses(
              MPFonts.bodyMedium,
              noContentStyles.noContentDisplayName
            )}
          >
            {profileData.displayName}
          </p>
          <p>hasn&#39;t liked any Artworks.</p>
        </div>
      )}
      <MPMasonry columns={GalleryColumnNum} spacing={2}>
        {data.map((release) =>
          !release.liveProduct ? null : (
            <DigitalMediaItem
              key={release.liveProduct.id}
              dm={release.digitalMedia}
              release={release}
              product={release.liveProduct}
              link={release.liveProduct.productUrl}
              canDrag={false}
            />
          )
        )}
      </MPMasonry>
      <div ref={visibilityRef}>&nbsp;</div>
      {!!loading && <DefaultLoader />}
    </div>
  );
}

function Likes() {
  const { profileSlug } = useParams();
  const [queryRef, loadQuery] = useQueryLoader<LikesQuery>(GetLikes);

  useEffect(() => {
    loadQuery({ first: 10, slug: profileSlug });
  }, [profileSlug, loadQuery]);

  return (
    <>
      <ProfilePageTabs currentTab="/:profileslug/likes" />
      <DefaultErrorBoundary>
        {!!queryRef && <LikesInner queryRef={queryRef} />}
      </DefaultErrorBoundary>
    </>
  );
}

export default Likes;
