import { useCallback } from 'react';

import {
  MPBackgroundColorClass,
  MPColorValue,
} from '@mp-frontend/core-components';
import { CheckCircleIcon } from '@mp-frontend/core-components/icons';
import { joinClasses } from '@mp-frontend/core-utils';

import DigitalMediaItem from 'components/DigitalMediaItem/DigitalMediaItem';
import { GalleryDigitalMediaRelease } from 'types/graphql/Gallery';
import emptyFunc from 'utils/emptyFunc';
import useOnEnterKey from 'utils/eventHandlers/useOnEnterKey';

import * as styles from 'css/pages/profiles/dialog/EditCategory/DigitalMediaSelectionItem.module.css';

interface IItemProps {
  item: { isSelected: boolean; release: GalleryDigitalMediaRelease };
  clickCallback?: (release: GalleryDigitalMediaRelease) => void;
}

export default function DigitalMediaSelectionItem({
  item,
  clickCallback = emptyFunc,
}: IItemProps) {
  const clickHander = useCallback(
    () => clickCallback(item.release),
    [clickCallback, item.release]
  );
  const ariaClickCallback = useOnEnterKey(clickHander);
  return (
    <div
      className={styles.digitalMediaItemWrapper}
      onClick={clickHander}
      onKeyPress={ariaClickCallback}
      aria-label="Select/Unselect Media"
      role="button"
      tabIndex={0}
    >
      <DigitalMediaItem
        className={styles.digitalMediaItem}
        key={item.release.id}
        dm={item.release.digitalMedia}
        product={item.release.liveProduct}
        release={item.release}
        showInfoBar={false}
      />
      <div className={styles.mask} data-isselected={item.isSelected}>
        <CheckCircleIcon
          fontSize="29"
          htmlColor={MPColorValue.CommonWhite as string}
          className={joinClasses(
            styles.checkIcon,
            MPBackgroundColorClass.TransparentPrimary
          )}
        />
      </div>
    </div>
  );
}
