import { atom, AtomEffect, useRecoilState } from 'recoil';

import {
  ActivitySortTypeEnum,
  CurrencyModeEnum,
  SortOrderEnum,
} from 'types/__generated__/graphql';

export function IsActivityFilterApplied(params: Record<string, any>): boolean {
  return Object.entries(params).some((entry) => {
    const [key, state] = entry;
    switch (key) {
      case 'sortOrder':
        return !(state[0] === SortOrderEnum.Desc || state[0] === undefined);
      case 'sortType':
        return state[0] !== ActivitySortTypeEnum.CreatedAt;
      default:
        return Object.values(state[0]).some(
          (filter) => filter && filter !== '0'
        );
    }
  });
}

function singleParamStore<T extends string>(
  key: string,
  defaultValue: T = undefined
): AtomEffect<T> {
  return ({ onSet, setSelf, trigger }) => {
    if (trigger === 'get') {
      const param = new URLSearchParams(window.location.search).get(key);
      setSelf(param ? (param as T) : defaultValue);
    }
    onSet((newValue) => {
      const params = new URLSearchParams(window.location.search);
      params.delete(key);
      if (newValue) params.set(key, newValue);
      window.history.replaceState(
        {},
        '',
        `${window.location.pathname}?${params}`
      );
    });
  };
}

const activitySortOrderAtom = atom<SortOrderEnum>({
  default: SortOrderEnum.Desc,
  effects: [singleParamStore<SortOrderEnum>('sortOrder', SortOrderEnum.Desc)],
  key: 'activity-sort-order-refresh',
});

const activitySortTypeAtom = atom<ActivitySortTypeEnum>({
  default: ActivitySortTypeEnum.CreatedAt,
  effects: [
    singleParamStore<ActivitySortTypeEnum>(
      'activitySortType-refresh',
      ActivitySortTypeEnum.CreatedAt
    ),
  ],
  key: 'activity-sort-type-refresh',
});

const currencyModeAtom = atom<CurrencyModeEnum>({
  default: CurrencyModeEnum.Usd,
  effects: [
    singleParamStore<CurrencyModeEnum>('sortType', CurrencyModeEnum.Usd),
  ],
  key: 'activity-currency-mode-refresh',
});

interface HookReturn {
  currencyMode: [CurrencyModeEnum, (s: CurrencyModeEnum) => void];
  sortOrder: [SortOrderEnum, (s: SortOrderEnum) => void];
  sortType: [ActivitySortTypeEnum, (s: ActivitySortTypeEnum) => void];
}

function useActivityFilterState(): HookReturn {
  const currencyMode = useRecoilState(currencyModeAtom);
  const sortOrder = useRecoilState(activitySortOrderAtom);
  const sortType = useRecoilState(activitySortTypeAtom);

  return {
    currencyMode,
    sortOrder,
    sortType,
  };
}

export default useActivityFilterState;
