import { useCallback, useEffect, useMemo, useRef } from 'react';
import { DragSourceMonitor, useDrag, useDrop } from 'react-dnd';

import DragTypes from 'types/enums/DragTypes';

export interface useDndProps {
  onDrop: () => void;
  onHover: (_a: any, _b: any) => void;
}

export default function useDnd(
  dropee,
  canDnd: boolean,
  dragType: DragTypes,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  onHover = (_a: unknown, _b: unknown) => undefined,
  onDrop = () => undefined,
  passedRefCallback = undefined
): [
  {
    style: {
      cursor: string;
      opacity: number;
    };
  },
  (node?: HTMLElement) => void
] {
  const ref = useRef(null);
  const refCallback = useCallback(
    (node) => {
      if (node) ref.current = node;
      if (node && passedRefCallback) passedRefCallback(node);
    },
    [passedRefCallback]
  );

  const [{ isDragging }, drag] = useDrag(
    () => ({
      canDrag: canDnd,
      collect: (monitor: DragSourceMonitor) => ({
        isDragging: monitor.isDragging(),
      }),
      item: dropee,
      type: dragType,
    }),
    [canDnd]
  );

  const dragStyle = useMemo(
    () => ({
      cursor: !canDnd ? 'inherit' : 'grab',
      // TODO(brian): Go over this with Tyler, and determine site-wide appropriateness
      opacity: isDragging ? 0.4 : 1,
    }),
    [isDragging, canDnd]
  );

  const [{ offset, draggedItem, isOver }, drop] = useDrop(
    () => ({
      accept: dragType,
      collect: (monitor) => ({
        draggedItem: monitor.getItem(),
        isOver: monitor.isOver(),
        offset: monitor.getClientOffset(),
      }),
      drop(_item: unknown, monitor) {
        if (!monitor.didDrop()) {
          onDrop();
          return dropee;
        }
        return undefined;
      },
    }),
    [canDnd, dropee.id]
  );

  useEffect(() => {
    if (canDnd && isOver) {
      onHover(draggedItem, dropee);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [offset?.x, offset?.y]);

  drag(drop(ref));

  return [{ style: dragStyle }, refCallback];
}
