import { ICreatorTooltipProps } from 'components/CreatorTooltip';

import HoverableProfileLink, {
  HoverableProfileLinkProps,
} from './HoverableProfileLink';

interface IUser {
  href: string;
  name: string;
}
type UserType = IUser & ICreatorTooltipProps['user'];

interface IProps {
  collaborators: Array<UserType>;
  creator: UserType;
  owner?: HoverableProfileLinkProps['owner'];
}

function Creators({ creator, collaborators, owner }: IProps) {
  return (
    <>
      <HoverableProfileLink
        key={creator.href}
        href={creator.href}
        name={creator.name}
        profile={creator}
        owner={owner}
      />
      {!!collaborators.length &&
        collaborators.map((collaborator) => (
          <>
            &#32;,
            <HoverableProfileLink
              key={collaborator.href}
              href={collaborator.href}
              name={collaborator.name}
              profile={collaborator}
              owner={owner}
            />
          </>
        ))}
    </>
  );
}

export default Creators;
