import { useCallback, useRef, useState } from 'react';

/**
 * This function memoizes gallery state for use with drag and drop. SInce useMemo is not a semantic guarantee, we
 * can't use that.
 *
 * The Gallery view requires an intermediate view, where the item positions, have been moved, but before the final position
 * has been determined and the request goes out to the server.
 */
function memoizeData(
  data,
  dependencies,
  /* eslint-disable no-param-reassign */
  backingRef: {
    data: Array<any>;
    dependencies: Array<any>;
  },
  key = 'id'
) {
  if (backingRef.dependencies) {
    if (
      dependencies.every((el, index) => el === backingRef.dependencies[index])
    ) {
      return backingRef.data;
    }
  } else {
    backingRef.dependencies = dependencies;
    /* eslint-disable-next-line no-return-assign */
    return (backingRef.data = data.map((identity) => identity));
  }
  const [dataLength, aId, aIndex, bId, bIndex] = dependencies;
  const [oldDataLength, , , ,] = backingRef.dependencies;
  if (oldDataLength < dataLength) {
    /* eslint-disable-next-line prefer-spread */
    backingRef.data.splice.apply(
      backingRef.data,
      [oldDataLength, 0].concat(data.splice(oldDataLength))
    );
  } else if (oldDataLength > dataLength) {
    backingRef.dependencies = dependencies;
    /* eslint-disable-next-line no-return-assign */
    return (backingRef.data = data.map((identity) => identity));
  }

  if (
    aIndex > -1 &&
    bIndex > -1 &&
    backingRef.data[aIndex][key] === aId &&
    backingRef.data[bIndex][key] === bId
  ) {
    // If the two DMs are next to each other, we want to swap them
    if (Math.abs(aIndex - bIndex) <= 1) {
      const temp = backingRef.data[aIndex];
      backingRef.data[aIndex] = backingRef.data[bIndex];
      backingRef.data[bIndex] = temp;
    } else {
      // otherwise, we want to insert the DM there, in our case always before the droppee,
      // unless its the very last one.
      const temp = backingRef.data.splice(aIndex, 1)[0];
      if (bIndex >= backingRef.data.length) {
        backingRef.data.push(temp);
      } else {
        backingRef.data.splice(bIndex - (aIndex > bIndex ? 0 : 1), 0, temp);
      }
    }
  }
  backingRef.dependencies = dependencies;
  return backingRef.data;
}

interface SwapItem {
  id: string | undefined;
  index: number;
}

/**
 * This provides shared functionality for implementing drag and drop for tabs and digital media items within a user gallery.
 *
 * Specifically, We need a derived state that can change in place when the real backing data changes. This provides the derived
 * ordering along with the required hover callback function needed to handle intermediate changes.
 */
export default function useBackingDndData<DT = any>(
  data: Array<DT>,
  dependencyArray: Array<any> = [],
  key = 'id'
): [Array<DT>, (dragee: SwapItem, dropee: SwapItem) => void, () => void] {
  const [swapState, setSwapState] = useState<Array<SwapItem>>([
    { id: undefined, index: -1 },
    { id: undefined, index: -1 },
  ]);
  const swapStateRef = useRef(swapState);
  swapStateRef.current = swapState;

  // We roll our own since useMemo is not a semantic guarantee
  const backingRef = useRef({
    data: null,
    dependencies: null,
  });
  const memoData: Array<DT> = memoizeData(
    data,
    [
      data.length,
      swapState[0].id,
      swapState[0].index,
      swapState[1].id,
      swapState[1].index,
    ].concat(dependencyArray),
    backingRef.current,
    key
  );

  const hoverCallback = useCallback(
    (dragee: SwapItem, dropee: SwapItem) => {
      const [original, current] = swapState;
      if (dragee.id === dropee.id) return;
      const drageeIndex = backingRef.current.data.findIndex(
        (el) => el[key] === dragee.id
      );
      const dropeeIndex = backingRef.current.data.findIndex(
        (el) => el[key] === dropee.id
      );
      if (
        original.id === dragee.id &&
        original.index === dragee.index &&
        current.id === dropee.id &&
        current.index === dropee.index
      )
        return;
      setSwapState([
        { id: dragee.id, index: drageeIndex },
        { id: dropee.id, index: dropeeIndex },
      ]);
    },
    [swapState, key]
  );

  const invalidateBackingRef = useCallback(() => {
    backingRef.current.dependencies = null;
  }, []);

  return [memoData, hoverCallback, invalidateBackingRef];
}
