import { MPFonts } from '@mp-frontend/core-components';
import { joinClasses } from '@mp-frontend/core-utils';

import CreatorTooltip, {
  ICreatorTooltipProps,
} from 'components/CreatorTooltip';

export interface HoverableProfileLinkProps {
  href: string;
  name: string;
  profile: ICreatorTooltipProps['user'];
  owner?: ICreatorTooltipProps['owner'];
}

export default function HoverableProfileLink({
  href,
  name,
  profile,
  owner,
}: HoverableProfileLinkProps) {
  return (
    <CreatorTooltip user={profile} owner={owner}>
      <a
        href={href}
        className="invisibleAnchor resetPointerEvents"
        target="_blank"
        rel="noreferrer"
      >
        <span className={joinClasses('reset', MPFonts.attributeText)}>
          {name}
        </span>
      </a>
    </CreatorTooltip>
  );
}
