import { HTMLAttributes } from 'react';

import { joinClasses } from '@mp-frontend/core-utils';

import * as styles from 'css/pages/profiles/banner/Banner.module.css';

export function ProfileImg({
  backgroundImage,
  children,
  className,
  ...passedProps
}: { backgroundImage: string } & HTMLAttributes<HTMLDivElement>) {
  return (
    <div
      {...passedProps}
      className={
        className ? joinClasses(className, styles.image) : styles.image
      }
      style={
        backgroundImage ? { backgroundImage: `url(${backgroundImage})` } : {}
      }
    >
      {children}
    </div>
  );
}

export function ProfileMask({ ...passedProps }: HTMLAttributes<SVGElement>) {
  return (
    <svg
      width="auto"
      height="auto"
      viewBox="0 0 1920 192"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      {...passedProps}
    >
      <path
        d="M1920 191.5H0V0C906 149 1356 99 1920 0V191.5Z"
        fill="currentColor"
      />
    </svg>
  );
}
