import { useOutletContext, useParams } from 'react-router-dom';
import { useGate } from 'statsig-react';

import { MPActionButton, MPFonts } from '@mp-frontend/core-components';

import { ProfileAccountType } from 'types/__generated__/graphql';

import DefaultErrorBoundary from 'components/ErrorBoundaries/DefaultErrorBoundary';
import ExploreGrid, { NoResultsProps } from 'components/explore/ExploreGrid';
import FilterRow from 'components/explore/FilterRow';
import { useMapQueryParamsToFilterStateEffect } from 'pages/explore/artwork/UseArtworkFilterState';

import ProfilePageTabs from './ProfilePageTabs';
import ShopCollectionFilters from './ShopCollectionFilters';
import useProfileData, { useProfileDataQueryRef } from './useProfileData';

function NoResults({ areFiltersApplied, resetFilters }: NoResultsProps) {
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);

  return areFiltersApplied ? (
    <div className="centerText">
      <h2 className={MPFonts.headline4}>
        There are no artworks with the filters you&#39;ve selected
      </h2>
      <p>
        Clear all filters to search all the artworks that&nbsp;
        {(profileData.accountType === ProfileAccountType.Collector ||
          profileData.accountType === ProfileAccountType.Anon) &&
          `${profileData.displayName} owns`}
        {(profileData.accountType === ProfileAccountType.Creator ||
          profileData.accountType === ProfileAccountType.Collection) &&
          `${profileData.displayName} has created`}
      </p>
      <MPActionButton onClick={resetFilters}>Clear All Filters</MPActionButton>
    </div>
  ) : (
    <div className="centerText">
      <h2 className={MPFonts.headline4}>
        {(profileData.accountType === ProfileAccountType.Collector ||
          profileData.accountType === ProfileAccountType.Anon) &&
          `${profileData.displayName} doesn't have any owned artworks for sale at this time`}
        {(profileData.accountType === ProfileAccountType.Creator ||
          profileData.accountType === ProfileAccountType.Collection) &&
          `${profileData.displayName} doesn't have any created artworks for sale at this time`}
      </h2>
    </div>
  );
}

export default function Shop() {
  const { profileSlug } = useParams();
  const { profileQueryRef } = useOutletContext<{
    profileQueryRef: useProfileDataQueryRef;
  }>();
  const profileData = useProfileData(profileQueryRef);
  useMapQueryParamsToFilterStateEffect();
  const gate = useGate('gate_show_collection_filters');
  const showCollectionFilters = !gate.isLoading && gate.value;
  return (
    <>
      <ProfilePageTabs currentTab="/:profileslug/shop" />
      <DefaultErrorBoundary>
        {showCollectionFilters &&
        profileData.accountType === ProfileAccountType.Collection ? (
          <ShopCollectionFilters
            profilesCollectionTraitsQuery={{ variables: { slug: profileSlug } }}
          />
        ) : (
          <FilterRow />
        )}
        <ExploreGrid slug={profileSlug} noResults={NoResults} />
      </DefaultErrorBoundary>
    </>
  );
}
